import SourceAbstract from 'Admin/Editor/Control/Source/SourceAbstract';
import ObjectRepository from 'App/Option/ObjectRepository';

export default class ContainerSource extends SourceAbstract
{
    public optionsRepository: ObjectRepository;

    protected optionKeyProp: string = null;

    public constructor(container?: object)
    {
        super();

        this.optionsRepository = new ObjectRepository();

        if (container)
        {
            this.optionsRepository.container = container;
        }
    }

    public set optionsContainer(container: object)
    {
        this.optionsRepository.container = container;
    }

    public get optionsContainer(): object
    {
        return this.optionsRepository.container;
    }

    public get optionKey(): string
    {
        return this.optionKeyProp ? this.optionKeyProp : this.control.optionKey;
    }

    public set optionKey(key: string)
    {
        this.optionKeyProp = key;
    }

    public saveValue(): void
    {
        this.optionsRepository.set(this.optionKey, this.optionValue);
    }

    public getValue(): any
    {
        return this.optionsRepository.has(this.optionKey) ? this.optionsRepository.get(this.optionKey): undefined;
    }
}


