import EditorComponentPanel from 'Admin/Editor/Panel/EditorComponentPanel';

import ExtendObject from 'System/Object/ExtendObject';

export default class FieldListPanel extends EditorComponentPanel
{
    public getValidatorOptions(): object
    {
        return ExtendObject(super.getValidatorOptions(), {
            rules: {
                entityTitle: {
                    required: true,
                    minlength: 2
                },
                optionKey: {
                    required: true,
                    filterKeyText: true,
                    filterKeyUniquer: true,
                    minlength: 2
                },
                itemsSource: {
                    required: true
                },
                itemsSourceAttribute: {
                    requiredIfActiveControl: true
                },
                itemsSourceCategory: {
                    requiredIfActiveControl: true
                },
                itemsSourceTaxonomy: {
                    requiredIfActiveControl: true
                },
                itemsDisplay: {
                    requiredIfActiveControl: true
                },
                queryType: {
                    requiredIfActiveControl: true
                },
                titleItemReset: {
                    requiredIfActiveControl: true,
                    minlength: 2
                },
            }
        });
    }
}


