import FieldComponent from 'Admin/Field/Editor/FieldComponent';
import IFilteringComponent from 'Admin/Editor/Component/IFilteringComponent';

export default class EditorComponent extends FieldComponent implements IFilteringComponent
{
    public getFilterKeys(): string[]
    {
        let keys = [];

        if (this.options.get('optionKeyFormat') == 'dash')
        {
            keys.push(this.options.get('optionKey'));
        }
        else if (this.options.get('optionKeyFormat') == 'two')
        {
            keys.push(this.options.get('minPriceOptionKey'));

            keys.push(this.options.get('maxPriceOptionKey'));
        }

        return keys;
    }
}


