import SelectiveComponent from 'Admin/Component/SelectiveComponent';
import EventManager from 'System/Event/EventManager';

import Context from 'System/Object/Context';
import CreateElement from 'System/Helper/CreateElement';
import ScrollPage from 'System/Helper/ScrollPage';
import ExtendObject from 'System/Object/ExtendObject';

import EntitiesWalk from 'App/Entity/Helper/EntitiesWalk';
import EntityStatus from 'App/Entity/EntityStatus';

import BaseComponent from 'Admin/Editor/Component/BaseComponent';

import BaseProjection from 'Admin/Editor/Projection/BaseProjection';
import ChildProjectionDirection from 'Admin/Editor/Projection/ChildProjectionDirection';

export default class ProjectionManager extends SelectiveComponent
{
    public rootSelector: string = '.page-editor-container .panel.projections-panel';

    protected activeProjectionsProp: BaseProjection[] = [];

    protected sortableSelectors: any;

    protected sortableRootSelectors: string;

    protected sortableOptions: any = {};

    protected sortingIsInitialized: boolean = false;

    public get activeProjections(): BaseProjection[]
    {
        return this.activeProjectionsProp;
    }

    public get containerElement(): JQuery
    {
        return this.rootElement.find('.projections-container');
    }

    public initialProperties(): void
    {
        this.app.componentRegister.set('Editor/ProjectionManager', this);

        this.sortableSelectors = [
            '.projections-container',
            '.sort-list'
        ];

        this.sortableRootSelectors = this.sortableSelectors.map((selector) => {
            return '.page-editor-container .projections-panel ' + selector;
        }).join(',');

        this.sortableOptions = {
            items: '.sort-item',
            acceptFrom: this.sortableSelectors.join(', '),
            placeholder: `<div class="projection projection-placeholder"></div>`,
            forcePlaceholderSize: false
        };
    }

    public attachEvents(manager: EventManager): void
    {
        manager.addSystemHandler(this.editorPackage.projectLoadedEvent, this.onProjectLoaded);

        manager.addWindowHandler('load', this.onWindowLoad);
    }

    protected onWindowLoad(event: Event): void
    {
        this.attachSortableEvents();

        sortable(this.sortableRootSelectors, this.sortableOptions);

        this.sortingIsInitialized = true;
    }

    protected reloadSortable(): void
    {
        if (!this.sortingIsInitialized)
        {
            return;
        }

        sortable(this.sortableRootSelectors, 'destroy');

        sortable(this.sortableRootSelectors, this.sortableOptions);

        this.attachSortableEvents();

        let serializeSortable = sortable(this.sortableRootSelectors, 'serialize') as any;

        for (let serializeIndex in serializeSortable)
        {
            let serializeItem = serializeSortable[serializeIndex];

            for (let index in serializeItem.items)
            {
                let item = serializeItem.items[index],
                    itemDomElement = CreateElement(item.node),
                    itemId = itemDomElement.data('entity-id');

                if (!this.register.components.has(itemId))
                {
                    continue;
                }

                let itemComponent = this.register.components.get(itemId);

                itemComponent.entityOrder = index;
            }
        }
    }

    protected attachSortableEvents(): void
    {
        let containerElements = this.app.bodyElement.find(this.sortableRootSelectors),
            flagKey = 'wcpf-is-attached-sortable-events';

        containerElements.each((index, containerDomElement) => {
            let containerElement = CreateElement(containerDomElement);

            if (!containerElement.data(flagKey))
            {
                containerDomElement.addEventListener(
                    'sortupdate',
                    Context(this.onSortUpdate, this)
                );

                containerElement.data(flagKey, true);
            }
        });
    }

    protected onSortUpdate(event: any): void
    {
        let prevContainer: any = {
            node: event.detail.origin.container,
            items: event.detail.origin.items,
        },  currentContainer: any = {
            node: event.detail.destination.container,
            items: event.detail.destination.items,
        },  elementItem: any = {
            node: event.detail.item,
        },  isSameContainer = false;


        for (let item of [currentContainer, prevContainer, elementItem])
        {
            item.editorComponent = null;

            item.projection = null;

            item.element = CreateElement(item.node);

            if (item.element.hasClass('sort-list'))
            {
                item.element = item.element.parents('.sort-item:first');
            }

            if (item.element.hasClass('projections-container'))
            {
                item.entityId = this.projectEntity.entityId;
            }
            else
            {
                item.entityId = item.element.data('entity-id');
            }

            if (this.register.components.has(item.entityId))
            {
                item.editorComponent = this.register.components.get(item.entityId);

                if (item.editorComponent && item.editorComponent.hasOwnProperty('projection'))
                {
                    item.projection = (item.editorComponent as any).projection;
                }
            }
        }

        if (currentContainer.element.get(0).isSameNode(
                prevContainer.element.get(0)
            ))
        {
            isSameContainer = true;
        }

        let eventArgs: any = {
            editorComponent: elementItem.editorComponent,
            nextContainerComponent: currentContainer.editorComponent,
            prevContainerComponent: prevContainer.editorComponent
        };

        if (elementItem.editorComponent)
        {
            if (currentContainer.editorComponent)
            {
                elementItem.editorComponent.entityParentId = currentContainer.editorComponent.entityId;
            }
            else
            {
                elementItem.editorComponent.entityParentId = this.projectEntity.entityId;
            }
        }

        if (elementItem.projection)
        {
            elementItem.projection.moveProjectionEvent.trigger(
                ExtendObject({
                    isSameContainer: isSameContainer
                }, eventArgs)
            );
        }

        if (isSameContainer)
        {
            if (currentContainer.projection
                && currentContainer.projection.hasOwnProperty('moveChildProjectionEvent'))
            {
                (currentContainer.projection as any).moveChildProjectionEvent.trigger(
                    ExtendObject({
                        direction: ChildProjectionDirection.same
                    }, eventArgs)
                );
            }
        }
        else
        {
            if (currentContainer.projection
                && currentContainer.projection.hasOwnProperty('moveChildProjectionEvent'))
            {
                (currentContainer.projection as any).moveChildProjectionEvent.trigger(
                    ExtendObject({
                        direction: ChildProjectionDirection.inside
                    }, eventArgs)
                );
            }

            if (prevContainer.projection
                && prevContainer.projection.hasOwnProperty('moveChildProjectionEvent'))
            {
                (prevContainer.projection as any).moveChildProjectionEvent.trigger(
                    ExtendObject({
                        direction: ChildProjectionDirection.outside
                    }, eventArgs)
                );
            }
        }

        for (let container of isSameContainer? [currentContainer]: [prevContainer, currentContainer])
        {
            for (let index in container.items)
            {
                let itemElement = CreateElement(container.items[index]),
                    itemId = 0;

                itemId = itemElement.data('entity-id');

                if (this.register.components.has(itemId))
                {
                    let itemEditorComponent = this.register.components.get(itemId);

                    itemEditorComponent.entityOrder = index;
                }
            }
        }
    }

    protected onProjectLoaded(): void
    {
        for (let editorComponent of this.register.components.values())
        {
            if (!this.prototypes.projection.has(editorComponent.entityKey)
                || !editorComponent.hasOwnProperty('projection'))
            {
                continue;
            }

            this.addProjection(editorComponent);
        }
    }

    public hasProjection(editorComponent: BaseComponent): boolean
    {
        for (let i in this.activeProjections)
        {
            let projectionItem = this.activeProjections[i];

            if (projectionItem.editorComponent.entityId == editorComponent.entityId)
            {
                return true;
            }
        }

        return false;
    }

    public addProjection(editorComponent: BaseComponent): void
    {
        if (this.hasProjection(editorComponent))
        {
            return;
        }

        if (!this.prototypes.projection.has(editorComponent.entityKey))
        {
            throw new Error(`Projection controller prototype with id '${editorComponent.entityKey}' not found`);
        }

        if (!editorComponent.hasOwnProperty('projection'))
        {
            throw new Error(`Element '#${editorComponent.entityId} - ${editorComponent.entityTitle}' does not have projection support`);
        }

        let projection = this.builders.projection.createProjectionByComponent(editorComponent),
            template = this.getTemplate(projection.projectionKey);

        if (template == null)
        {
            throw new Error(`Projection template with id '${projection.projectionKey}' not found`);
        }

        this.activeProjectionsProp.push(projection);

        projection.initialProperties();

        let domElement = CreateElement(
            template(
                projection.getTemplateContext()
            )
        );

        projection.componentElement = domElement;

        let needInsertInContainer = true;

        if (editorComponent.entityParentId && this.register.components.has(editorComponent.entityParentId))
        {
            let parentComponentItem = this.register.components.get(editorComponent.entityParentId),
                parentIsProjectionSupport = false;

            while(true)
            {
                if (parentComponentItem.hasOwnProperty('projection'))
                {
                    parentIsProjectionSupport = true;

                    break;
                }

                if (this.register.components.has(parentComponentItem.entityParentId))
                {
                    parentComponentItem = this.register.components.get(parentComponentItem.entityParentId);
                }
                else
                {
                    break;
                }
            }

            if (parentIsProjectionSupport)
            {
                if (!(parentComponentItem as any).projection
                    && this.prototypes.projection.has(parentComponentItem.entityKey))
                {
                    this.addProjection(parentComponentItem);
                }

                let parentProjection = parentComponentItem.projection;

                if (parentProjection && 'insertChildComponentElement' in parentProjection)
                {
                    parentProjection.insertChildComponentElement(domElement, editorComponent);

                    needInsertInContainer = false;
                }
            }
        }

        if (needInsertInContainer)
        {
            this.containerElement.append(domElement);
        }

        projection.initialProjection();

        projection.attachEvents(projection.eventManager);

        if (editorComponent.entity.childEntities.length)
        {
            for (let i in editorComponent.entity.childEntities)
            {
                let childEntity = editorComponent.entity.childEntities[i],
                    childComponent = this.register.components.get(childEntity.entityId);

                if (childComponent && childComponent.hasOwnProperty('projection'))
                {
                    this.addProjection(childComponent);
                }
            }
        }

        this.reloadSortable();

        this.triggerZoneState();
    }

    public moveProjection(moveEditorComponent: BaseComponent, containerEditorComponent: BaseComponent): void
    {
        if (!moveEditorComponent.hasOwnProperty('projection'))
        {
            throw new Error(`Element '#${moveEditorComponent.entityId} - ${moveEditorComponent.entityTitle}' does not have projection support`);
        }

        let needInsertInProjectContainer = true,
            moveProjection = (moveEditorComponent as any).projection,
            originDomElement = this.containerElement,
            destinationDomElement = this.containerElement;

        if (this.register.components.has(moveEditorComponent.entity.parentId))
        {
            let originComponent = this.register.components.get(moveEditorComponent.entity.parentId);

            if (originComponent.hasOwnProperty('projection')
                && (originComponent as any).projection)
            {
                originDomElement = (originComponent as any).projection.componentElement;
            }
        }

        if (containerEditorComponent.hasOwnProperty('projection'))
        {
            let containerProjection = (containerEditorComponent as any).projection;

            if (containerProjection && 'insertChildComponentElement' in containerProjection)
            {
                containerProjection.insertChildComponentElement(
                    (moveEditorComponent as any).projection.componentElement,
                    moveEditorComponent
                );

                destinationDomElement = containerProjection.componentElement;

                needInsertInProjectContainer = false;
            }
        }

        if (needInsertInProjectContainer)
        {
            this.containerElement.append((moveEditorComponent as any).projection.componentElement);
        }

        let childSelectors = '> ' + this.sortableSelectors.join(', > ');

        this.onSortUpdate({
            detail: {
                origin: {
                    container: originDomElement.get(0),
                    items: originDomElement.find(childSelectors).toArray()
                },
                destination: {
                    container: destinationDomElement.get(0),
                    items: destinationDomElement.find(childSelectors).toArray()
                },
                item: (moveEditorComponent as any).projection.componentElement.get(0)
            }
        });
    }

    public removeProjection(editorComponent: BaseComponent, needRemoveComponent?: boolean): void
    {
        if (!editorComponent.hasOwnProperty('projection'))
        {
            throw new Error(`Element '#${editorComponent.entityId} - ${editorComponent.entityTitle}' does not have projection support`);
        }

        if (!confirm(this.localizedMessages.projection['shouldRemoveElement']))
        {
            return;
        }

        let projection = (editorComponent as any).projection;

        if (!projection)
        {
            return;
        }

        if (editorComponent.entity.childEntities.length)
        {
            for (let childEntity of editorComponent.entity.childEntities)
            {
                if (this.register.components.has(childEntity.entityId))
                {
                    this.moveProjection(
                        this.register.components.get(childEntity.entityId),
                        this.projectEditorComponent
                    );
                }
            }
        }

        if (needRemoveComponent === undefined || needRemoveComponent)
        {
            editorComponent.entityStatus = EntityStatus.remove;

            if (editorComponent.entity.childEntities.length)
            {
                EntitiesWalk(editorComponent.entity, (entity) => {
                    let component = this.register.components.get(entity.entityId);

                    component.status = EntityStatus.remove;
                });
            }
        }

        for (let panel of this.panelManager.activePanels)
        {
            if (panel.hasOwnProperty('editorComponent')
                && (panel as any).editorComponent.entityId == editorComponent.entityId)
            {
                this.panelManager.backPanel({
                    ignoreValidation: true
                });

                break;
            }
        }

        projection.componentElement.remove();

        this.activeProjectionsProp = this.activeProjectionsProp.filter(
            (item) => item.editorComponent.entityId != editorComponent.entityId
        );

        (editorComponent as any).projection = null;

        this.reloadSortable();

        this.triggerZoneState();
    }

    public scrollToProjection(editorComponent: BaseComponent): void
    {
        if (!editorComponent.hasOwnProperty('projection'))
        {
            return;
        }

        let projectionElement = (editorComponent as any).projection.componentElement,
            topPosition = projectionElement.offset().top - (window.innerHeight / 2);

        ScrollPage(topPosition, 500);
    }

    protected triggerZoneState(): void
    {
        let zoneElement = this.rootElement.find('.projections-zone');

        if (this.activeProjections.length)
        {
            zoneElement.removeClass('empty-state');
        }
        else
        {
            zoneElement.addClass('empty-state');
        }
    }

    protected getTemplate(key: string): (context?: object) => HTMLElement
    {
        let templateElement = this.app.bodyElement.find(`.wcpf-projection-template[data-projection-key=${key}]`);

        if (!templateElement.length)
        {
            return null;
        }

        let id = templateElement.attr('id');

        if (!id || id.indexOf('tmpl-') !== 0)
        {
            return null;
        }

        return wp.template(id.slice(5));
    }
}


