import PanelLayoutAbstract from 'Admin/Editor/PanelLayout/PanelLayoutAbstract';
import EventManager from 'System/Event/EventManager';

import CreateElement from 'System/Helper/CreateElement';
import CreateElementByEvent from 'System/Helper/CreateElementByEvent';

import IPanelStructure from 'Admin/Editor/PanelLayout/IPanelStructure';

export default class TabsLayout extends PanelLayoutAbstract
{
    public attachEvents(manager: EventManager): void
    {
        super.attachEvents(manager);

        manager.addElementHandler(
            this.componentElement,
            'click',
            '.tabs-heading-wrapper .tab-heading:not(.active-tab)',
            this.onTabClick
        );

        if ('validate' in this.panel && (this.panel as any).hasOwnProperty('validateEvent'))
        {
            manager.addSystemHandler(
                (this.panel as any).validateEvent,
                this.onValidatePanel
            );
        }
    }

    protected onValidatePanel(): void
    {
        this.componentElement.find('.tab-content').each((index, domElement) => {
            let contentElement = CreateElement(domElement),
                tabCode = contentElement.data('tab-id'),
                titleElement = this.componentElement.find(`.tab-heading[data-tab-id="${tabCode}"]`),
                existsErrors = false;

            for (let errorDomElement of contentElement.find('.error:not(.control-element)'))
            {
                let errorElement = CreateElement(errorDomElement);

                if (errorElement.html().length)
                {
                    existsErrors = true;

                    break;
                }
            }

            if (existsErrors)
            {
                titleElement.addClass('exists-errors');
            }
            else
            {
                titleElement.removeClass('exists-errors');
            }
        });
    }

    protected onTabClick(event: Event): void
    {
        let headingElement = CreateElementByEvent(event),
            tabId = headingElement.data('tab-id');

        this.componentElement.find('.tab-heading.active-tab,.tab-content.active-tab')
            .removeClass('active-tab');

        headingElement.addClass('active-tab');

        this.componentElement.find(`.tab-content[data-tab-id="${tabId}"]`)
            .addClass('active-tab');
    }

    public assembleControls(structure: IPanelStructure): void
    {
        let tabs = (structure as any).tabs;

        for (let i in tabs)
        {
            let tabControls = tabs[i];

            if (! tabControls.length)
            {
                continue;
            }

            for (let j in tabControls)
            {
                let tabControl = tabControls[j];

                this.createControl(tabControl);
            }
        }
    }
}


