import LayoutProjectionAbstract from 'Admin/Layout/Editor/LayoutProjectionAbstract';
import BaseComponent from 'Admin/Editor/Component/BaseComponent';
import EventManager from 'System/Event/EventManager';

export default class EditorProjection extends LayoutProjectionAbstract
{
    public get titleElement(): JQuery
    {
        return this.componentElement.find(`.title-container.entity-id-${this.editorComponent.entityId} .entity-title`);
    }

    public get containerElement(): JQuery
    {
        return this.componentElement.find(`.sort-list.entity-id-${this.editorComponent.entityId}`);
    }

    public attachEvents(manager: EventManager): void
    {
        super.attachEvents(manager);

        manager.addSystemHandler(
            this.editorComponent.titleChangedEvent,
            this.onTitleChanged
        );
    }

    protected onTitleChanged(args: any): void
    {
        this.titleElement.html(args.title);
    }

    public insertChildComponentElement(domElement: JQuery, editorComponent: BaseComponent): void
    {
        this.containerElement.append(domElement);
    }
}


