import ElementComponent from 'Admin/Component/ElementComponent';
import EventManager from 'System/Event/EventManager';
import ApplicationEvent from 'System/ApplicationEvent';

import Entity from 'App/Entity/Entity';

import BaseComponent from 'Admin/Editor/Component/BaseComponent';
import ObjectRepository from 'App/Option/ObjectRepository';

import IMoveProjectionEventArgs from 'Admin/Editor/Projection/EventArgs/IMoveProjectionEventArgs';

export default class BaseProjection extends ElementComponent
{
    public projectionKey: string;

    public editorComponent: BaseComponent;

    public moveProjectionEvent: ApplicationEvent<IMoveProjectionEventArgs>;

    public constructor()
    {
        super();

        this.moveProjectionEvent = new ApplicationEvent<IMoveProjectionEventArgs>();
    }

    public attachEvents(manager: EventManager): void {}

    public initialProjection(): void {}

    public get entity(): Entity
    {
        return this.editorComponent.entity;
    }

    public get entityOptions(): ObjectRepository
    {
        return this.editorComponent.options;
    }

    public getTemplateContext(): object
    {
        return {};
    }
}


