import InputControlAbstract from 'Admin/Editor/Control/InputControlAbstract';
import EventManager from 'System/Event/EventManager';

import CreateElementByEvent from 'System/Helper/CreateElementByEvent';

export default class ElementListControl extends InputControlAbstract
{
    public attachEvents(manager: EventManager): void
    {
        manager.addElementHandler(
            this.componentElement,
            'click',
            '.element-item[data-element-code]',
            this.onItemClick
        );
    }

    protected onItemClick(event: Event): void
    {
        let itemElement = CreateElementByEvent(event);

        this.setElementValue({
            elementCode: itemElement.data('element-code'),
            defaultState: itemElement.data('default-state')
        });

        this.updateValueFromElement();
    }

    public setElementValue(value: any): void
    {
        let selectedElement = this.componentElement.find('.element-item[data-element-code].selected');

        selectedElement.removeClass('selected');

        if (typeof value != 'object')
        {
            return;
        }

        let itemElement = this.componentElement.find(`.element-item[data-element-code="${value.elementCode}"]`);

        itemElement.addClass('selected');
    }

    public getElementValue(): any
    {
        let selectedElement = this.componentElement.find('.element-item[data-element-code].selected');

        if (selectedElement.length)
        {
            return {
                elementCode: selectedElement.data('element-code'),
                defaultState: selectedElement.data('default-state')
            };
        }

        return null;
    }
}


