import ComponentAbstract from 'Admin/Component/ComponentAbstract';
import EventManager from 'System/Event/EventManager';

import BaseControl from 'Admin/Editor/Control/BaseControl';
import ObjectRepository from 'App/Option/ObjectRepository';

import CreateElement from 'System/Helper/CreateElement';

export default class ControlLoader extends ComponentAbstract
{
    protected controlRequests: Map<BaseControl, any>;

    public constructor()
    {
        super();

        this.controlRequests = new Map<BaseControl, any>();
    }

    public initialProperties()
    {
        this.app.componentRegister.set('Editor/Control/ControlLoader', this);
    }

    public attachEvents(manager: EventManager): void {}

    public reloadControls(controls: BaseControl[], callback?: (controls) => void): void
    {
        let props = [];

        for (let control of controls.values())
        {
            props.push(this.collectControlProps(control));
        }

        this.reloadRequest(props, controls, callback);
    }

    public reloadControl(control: BaseControl, callback?: (controls) => void): void
    {
        this.reloadRequest([this.collectControlProps(control)], [control], callback);
    }

    protected collectControlProps(control: BaseControl): any
    {
        let valuesRepository = new ObjectRepository({});

        for (let childControl of control.panelLayout.controls.values())
        {
            valuesRepository.set(childControl.optionKey, childControl.optionValue);
        }

        let parentsContext = {},
            context = {},
            controls = [control],
            path = [],
            parentControl = control.parentControl,
            entity = {};

        if (control.panel.hasOwnProperty('editorComponent'))
        {
            entity = (control.panel as any).editorComponent.entity;
        }

        while (parentControl instanceof BaseControl)
        {
            controls.push(parentControl);

            parentControl = parentControl.parentControl;
        }

        controls.reverse();

        for (let childControl of controls.values())
        {
            path.push(childControl.optionKey);

            for (let [contextControlIndex, contextControlItem] of childControl.contextControls.entries())
            {
                context[contextControlIndex] = contextControlItem.optionValue;
            }

            parentsContext[childControl.optionKey] = Object.assign({}, context);
        }

        return {
            options: valuesRepository.container,
            context: context,
            optionKey: control.optionKey,
            panelKey: control.panel.panelKey,
            controlPath: path,
            parentsContext: parentsContext,
            entity: entity
        };
    }

    protected reloadRequest(props: object, controls: BaseControl[], callback?: (controls) => void): void
    {
        for (let control of controls.values())
        {
            if (this.controlRequests.has(control))
            {
                this.controlRequests.get(control).abort();
            }

            control.componentElement.block();
        }

        let request = wp.ajax.send('wcpf_editor_control_reload', {
            type: 'POST',
            dataType: 'json',
            data: {
                controls: JSON.stringify(props)
            }
        }).done((data) => {
            if (data.items && Array.isArray(data.items))
            {
                for (let [itemIndex, itemData] of data.items.entries())
                {
                    if (typeof itemData != 'object')
                    {
                        continue;
                    }

                    let control = controls[itemIndex],
                        reloadProps = {
                            prevStructure: control.controlStructure,
                            structure: itemData.structure,
                            controlHtml: itemData.controlHtml,
                            controlElement: CreateElement(itemData.controlHtml)
                        };

                    control.controlStructure = itemData.structure;

                    if ('reloadControl' in control)
                    {
                        (control as any).reloadControl(reloadProps);
                    }
                }

                if (callback)
                {
                    callback(controls);
                }
            }
        }).always(() => {
            for (let control of controls.values())
            {
                control.componentElement.unblock();
            }
        });

        for (let control of controls.values())
        {
            this.controlRequests.set(control, request);
        }
    }
}


