import ComponentAbstract from 'Admin/Component/ComponentAbstract';
import EventManager from 'System/Event/EventManager';

import PanelLayoutAbstract from 'Admin/Editor/PanelLayout/PanelLayoutAbstract';

import BaseControl from 'Admin/Editor/Control/BaseControl';
import IControlStructure from 'Admin/Editor/Control/IControlStructure';

export default class Builder extends ComponentAbstract
{
    public initialProperties(): void
    {
        this.app.componentRegister.set('Editor/Builder/Control', this);
    }

    public attachEvents(manager: EventManager): void {}

    public createControl(structure: IControlStructure, panelLayout?: PanelLayoutAbstract): BaseControl
    {
        if (!this.prototypes.control.has(structure.controlKey))
        {
            throw new Error(`Control class not found by type '${structure.controlKey}'`);
        }

        let controlPrototype = this.prototypes.control.get(structure.controlKey),
            control = this.componentBuilder.build(controlPrototype as any) as BaseControl;

        if (!this.prototypes.controlSource.has(structure.controlSource))
        {
            throw new Error(`Control source by key '${structure.controlSource}' not found`);
        }

        let sourcePrototype = this.prototypes.controlSource.get(structure.controlSource),
            controlSource = this.componentBuilder.build(sourcePrototype);

        controlSource.control = control;

        control.controlSource = controlSource;

        if (panelLayout)
        {
            control.panelLayout = panelLayout;
        }

        control.controlStructure = structure;

        return control;
    }
}


