import InputControlAbstract from 'Admin/Editor/Control/InputControlAbstract';
import EventManager from 'System/Event/EventManager';
import ReloadControl from 'Admin/Editor/Control/Decorators/ReloadControl';

import IReloadControl from 'Admin/Editor/Control/IReloadControl';

import CreateElement from 'System/Helper/CreateElement';
import ObjectToArray from 'System/Object/ObjectToArray';

@ReloadControl
export default class CheckListControl extends InputControlAbstract implements IReloadControl
{
    public attachEvents(manager: EventManager): void
    {
        super.attachEvents(manager);

        manager.addElementHandler(
            this.componentElement,
            'change',
            '.control-element',
            this.updateValueFromElement
        );
    }

    public getEmptyValue(): any
    {
        return [];
    }

    public setElementValue(value: any): void
    {
        if (value == undefined || value == null)
        {
            value = [];
        }
        else if (typeof value == 'string')
        {
            value = [value];
        }
        else if (!Array.isArray(value) && typeof value == 'object')
        {
            value = ObjectToArray(value);
        }

        let checkedElement = this.controlElement.filter(':checked');

        checkedElement.prop('checked', false);

        if (Array.isArray(value))
        {
            for (let item of value.values())
            {
                let selectedElement = this.controlElement.filter(`[value="${item}"]`);

                selectedElement.prop('checked', true);
            }
        }
    }

    public getElementValue(): any
    {
        let values = [];

        this.controlElement.filter(':checked').map((index, domElement) => {
            let checkboxElement = CreateElement(domElement);

            values.push(checkboxElement.val());
        });

        return values;
    }

    public reloadControl(props: any): void
    {
        let newList = props.controlElement.find('.check-box-list');

        this.componentElement.find('.check-box-list').html(newList.html());
    }
}


