import SelectiveComponent from 'Admin/Component/SelectiveComponent';
import EventManager from 'System/Event/EventManager';

import CreateElement from 'System/Helper/CreateElement';
import ObjectCompare from 'System/Object/Compare';
import ScrollPage from 'System/Helper/ScrollPage';
import ExtendObject from 'System/Object/ExtendObject';

import BasePanel from 'Admin/Editor/Panel/BasePanel';
import PanelStatus from 'Admin/Editor/Panel/PanelStatus';

export default class PanelManager extends SelectiveComponent
{
    public rootSelector: string = '.page-editor-container .panel.options-panel';

    public readonly activePanels: BasePanel[] = [];

    public initialProperties(): void
    {
        this.app.componentRegister.set('Editor/PanelManager', this);
    }

    public attachEvents(manager: EventManager): void
    {
        manager.addComponentHandler(
            'click',
            '.action-back-panel-on-click',
            this.backPanel
        );
    }

    public checkIfPanelIsOpen(key: string, context: object, entityId?: any): boolean
    {
        for (let i in this.activePanels)
        {
            let panelItem = this.activePanels[i];

            if (entityId && panelItem.hasOwnProperty('editorComponent'))
            {
                if ((panelItem as any).editorComponent.entityId == entityId
                    && panelItem.panelKey == key
                    && ObjectCompare(context, panelItem.panelContext))
                {
                    return true;
                }
            }
            else
            {
                if (panelItem.panelKey == key
                    && ObjectCompare(context, panelItem.panelContext))
                {
                    return true;
                }
            }
        }

        return false;
    }

    public getCurrentPanel()
    {
        if (this.activePanels.length)
        {
            return this.activePanels[this.activePanels.length - 1];
        }

        return null;
    }

    public openPanel(key: string, context?: object, entityId?: any): void
    {
        if (typeof context == 'string' || typeof context == 'number')
        {
            entityId = context;

            context = {};
        }

        if (!key.length)
        {
            throw new Error('Parameter panel key is empty');
        }

        if (!context)
        {
            context = {};
        }

        if (this.checkIfPanelIsOpen(key, context, entityId))
        {
            return;
        }

        if (this.activePanels.length)
        {
            let prevPanel = this.activePanels[this.activePanels.length - 1];

            if (!this.checkCorrectnessOfOptions(prevPanel))
            {
                return;
            }

            this.modalToSaveOptions(prevPanel);

            if (!this.checkCorrectnessOfOptions(prevPanel))
            {
                return;
            }
        }

        let template = this.getTemplate(key);

        if (template == null)
        {
            throw new Error(`Panel template with id '${key}' not found`);
        }

        let panelLayout = this.builders.panelLayout.createPanelLayout(key),
            panel = this.builders.panel.createPanel(key, context, entityId);

        panelLayout.panel = panel;

        panel.panelLayout = panelLayout;

        panel.package = this.package;

        panel.initialProperties();

        let domElement = CreateElement(
            template(
                panel.getTemplateContext(panel.panelContext)
            )
        );

        domElement.hide();

        this.rootElement.append(domElement);


        // save dom element in component and panel
        panel.componentElement = panelLayout.componentElement = domElement;

        // add package and initial panel component
        this.componentBuilder.implementation(panelLayout, this.package);

        // create controls objects by panel structure
        panelLayout.assembleControls(panelLayout.panelStructure);

        panel.attachEvents(panel.eventManager);

        panel.status = PanelStatus.Launched;

        if (this.activePanels.length)
        {
            let prevPanel = this.activePanels[this.activePanels.length - 1],
                prevDomElement = prevPanel.componentElement;

            prevPanel.status = PanelStatus.Inactive;

            if (this.activePanels.length > 1)
            {
                prevPanel.status = PanelStatus.Destroyed;

                if (prevPanel.hasOwnProperty('editorComponent'))
                {
                    let prevEditorComponent = (prevPanel as any).editorComponent;

                    if (prevEditorComponent.hasOwnProperty('panels'))
                    {
                        (prevEditorComponent as any).panels.delete(prevPanel);
                    }
                }

                this.activePanels.pop();

                this.animationOpen(
                    prevDomElement,
                    domElement,
                    (prev: JQuery, next: JQuery) => {
                        prev.remove();
                    }
                );
            }
            else
            {
                this.animationOpen(prevDomElement, domElement);
            }
        }
        else
        {
            domElement.show();

            domElement.addClass('status-active');
        }

        panel.status = PanelStatus.Active;

        this.activePanels.push(panel);
    }

    public backPanel(args?: any): void
    {
        if (!args)
        {
            args = {};
        }

        args = ExtendObject({
            ignoreValidation: false
        }, args);

        if (this.activePanels.length <= 1)
        {
            return;
        }

        let prevPanel = this.activePanels[this.activePanels.length - 1],
            prevDomElement = prevPanel.componentElement;

        if (!args.ignoreValidation)
        {
            if (!this.checkCorrectnessOfOptions(prevPanel))
            {
                return;
            }
        }

        this.modalToSaveOptions(prevPanel);

        if (!args.ignoreValidation)
        {
            if (!this.checkCorrectnessOfOptions(prevPanel))
            {
                return;
            }
        }

        let nextPanel = this.activePanels[this.activePanels.length - 2],
            nextDomElement = nextPanel.componentElement;

        prevPanel.status = PanelStatus.Inactive;

        nextPanel.status = PanelStatus.Active;

        prevPanel.status = PanelStatus.Destroyed;

        if (prevPanel.hasOwnProperty('editorComponent'))
        {
            let prevEditorComponent = (prevPanel as any).editorComponent;

            if (prevEditorComponent.hasOwnProperty('panels'))
            {
                (prevEditorComponent as any).panels.delete(prevPanel);
            }
        }

        this.activePanels.pop();

        this.animationBack(
            prevDomElement,
            nextDomElement,
            (prev: JQuery, next: JQuery) => {
                prev.remove();
            }
        );
    }

    public checkCorrectnessOfOptions(panel: BasePanel): boolean
    {
        if ('validate' in panel)
        {
            return (panel as any).validate();
        }

        return true;
    }

    public modalToSaveOptions(panel: BasePanel): void
    {
        if (!panel.isControlsSaved)
        {
            let message = this.localizedMessages.panel.shouldSaveOptionsOnPanel;

            message = message.replace(/--panel-title/g, panel.panelLayout.panelTitle);

            if (confirm(message))
            {
                panel.saveValuesOfControls();
            }
            else
            {
                panel.resetValuesOfControls();
            }
        }
    }

    protected scrollToPanelElement(panelElement: JQuery): void
    {
        let adminBarElement = CreateElement('#wpadminbar'),
            panelTop = panelElement.offset().top,
            difference = window.scrollY - panelTop;

        if (difference > 200 || difference < -200)
        {
            let topPosition = panelTop - 20;

            if (adminBarElement.css('position') == 'fixed')
            {
                topPosition -= adminBarElement.height();
            }

            ScrollPage(topPosition, 500);
        }
    }

    protected animationOpen(prevElement: JQuery, nextElement: JQuery, callback?: (prev: JQuery, next: JQuery) => void): void
    {
        nextElement.addClass('status-launched');

        nextElement.show();

        prevElement.removeClass('status-active')
            .addClass('status-inactive');

        nextElement.addClass('status-active');

        setTimeout(() => {
            nextElement.removeClass('status-launched');

            if (callback)
            {
                callback(prevElement, nextElement);
            }
        }, 600);

        this.scrollToPanelElement(nextElement);
    }

    protected animationBack(prevElement: JQuery, nextElement: JQuery, callback?: (prev: JQuery, next: JQuery) => void): void
    {
        prevElement.removeClass('status-active')
            .addClass('status-destroyed');

        nextElement.removeClass('status-inactive')
            .addClass('status-active');

        setTimeout(() => {
            if (callback)
            {
                callback(prevElement, nextElement);
            }
        }, 600);
    }

    protected getTemplate(key: string): (context?: object) => HTMLElement
    {
        let templateElement = this.app.bodyElement.find(`.wcpf-panel-template[data-panel-key=${key}]`);

        if (!templateElement.length)
        {
            return null;
        }

        let id = templateElement.attr('id');

        if (!id || id.indexOf('tmpl-') !== 0)
        {
            return null;
        }

        return wp.template(id.slice(5));
    }
}


