import SystemApplication from 'System/Application';
import PackageBuilder from 'System/Builder/PackageBuilder';
import ComponentBuilder from 'System/Builder/ComponentBuilder';

import EntityPackage from 'App/Entity/Package';
import TemplatePackage from 'App/Template/Package';
import EditorPackage from 'Admin/Editor/Package';
import ValidatorPackage from 'Admin/Validator/Package';
import ProjectPackage from 'Admin/Project/Package';
import FieldPackage from 'Admin/Field/Package';
import LayoutPackage from 'Admin/Layout/Package';

export default class Application extends SystemApplication
{
    public initialization()
    {
        let packageBuilder = new PackageBuilder(),
            componentBuilder = new ComponentBuilder();

        this.objectContainer.set('ComponentBuilder', componentBuilder);

        packageBuilder.build(
            new TemplatePackage(),
            this
        );

        packageBuilder.build(
            new EntityPackage(),
            this
        );

        packageBuilder.build(
            new EditorPackage(),
            this
        );

        packageBuilder.build(
            new ValidatorPackage(),
            this
        );

        packageBuilder.build(
            new ProjectPackage(),
            this
        );

        packageBuilder.build(
            new FieldPackage(),
            this
        );

        packageBuilder.build(
            new LayoutPackage(),
            this
        );
    }
}


