import ComponentAbstract from 'Admin/Component/ComponentAbstract';
import EventManager from 'System/Event/EventManager';
import ApplicationEvent from 'System/ApplicationEvent';

import Entity from 'App/Entity/Entity';
import EntityStatus from 'App/Entity/EntityStatus';

import ObjectRepository from 'App/Option/ObjectRepository';

import ITitleChangedEventArgs from 'Admin/Editor/Component/EventArgs/ITitleChangedEventArgs';
import IParentChangedEventArgs from 'Admin/Editor/Component/EventArgs/IParentChangedEventArgs';
import IStatusChangedEventArgs from 'Admin/Editor/Component/EventArgs/IStatusChangedEventArgs';
import IOrderChangedEventArgs from 'Admin/Editor/Component/EventArgs/IOrderChangedEventArgs';

export default class BaseComponent extends ComponentAbstract
{
    public entity: Entity;

    public readonly options: ObjectRepository;

    public readonly titleChangedEvent: ApplicationEvent<ITitleChangedEventArgs>;

    public readonly parentChangedEvent: ApplicationEvent<IParentChangedEventArgs>;

    public readonly statusChangedEvent: ApplicationEvent<IStatusChangedEventArgs>;

    public readonly orderChangedEvent: ApplicationEvent<IOrderChangedEventArgs>;

    public constructor()
    {
        super();

        this.options = new ObjectRepository();

        this.titleChangedEvent = new ApplicationEvent<ITitleChangedEventArgs>();

        this.parentChangedEvent = new ApplicationEvent<IParentChangedEventArgs>();

        this.statusChangedEvent = new ApplicationEvent<IStatusChangedEventArgs>();

        this.orderChangedEvent = new ApplicationEvent<IOrderChangedEventArgs>();
    }

    public get entityId(): any
    {
        return this.entity.entityId;
    }

    public get entityKey(): string
    {
        return this.entity.entityKey;
    }

    public get entityTitle(): string
    {
        return this.entity.title;
    }

    public set entityTitle(value: string)
    {
        let oldValue = this.entity.title;

        this.entity.title = value;

        this.titleChangedEvent.trigger({
            title: value,
            oldTitle: oldValue,
            entity: this.entity,
            editorComponent: this
        });
    }

    public get entityParentId(): any
    {
        return this.entity.parentId;
    }

    public set entityParentId(value: any)
    {
        let oldValue = this.entity.parentId;

        this.entity.parentId = value;

        this.parentChangedEvent.trigger({
            parent: value,
            oldParent: oldValue,
            entity: this.entity,
            editorComponent: this
        });
    }

    public get entityStatus(): EntityStatus
    {
        return this.entity.status;
    }

    public set entityStatus(value: EntityStatus)
    {
        let oldStatus = this.entity.status;

        this.entity.status = value;

        this.statusChangedEvent.trigger({
            status: value,
            oldStatus: oldStatus,
            entity: this.entity,
            editorComponent: this
        });
    }

    public get entityOrder(): number
    {
        return this.entity.order;
    }

    public set entityOrder(value: number)
    {
        let oldOrder = this.entity.order;

        this.entity.order = value;

        this.orderChangedEvent.trigger({
            order: value,
            oldOrder: oldOrder,
            entity: this.entity,
            editorComponent: this
        });
    }

    public initialProperties(): void
    {
        this.options.container = this.entity.options;
    }

    public attachEvents(manager: EventManager): void
    {
        manager.addSystemHandler(this.parentChangedEvent, this.moveEntityWhenParentChange);
    }

    protected moveEntityWhenParentChange(args: any): void
    {
        if (!this.register.components.has(args.oldParent)
            || !this.register.components.has(args.parent))
        {
            return;
        }

        let oldEditorComponent = this.register.components.get(args.oldParent),
            oldEntityContainer = oldEditorComponent.entity,
            newEditorComponent = this.register.components.get(args.parent),
            newEntityContainer = newEditorComponent.entity;

        oldEntityContainer.childEntities = oldEntityContainer.childEntities.filter((item) => {
            return item.entityId != this.entityId;
        });

        newEntityContainer.childEntities.push(this.entity);
    }
}


