import LayoutProjectionAbstract from 'Admin/Layout/Editor/LayoutProjectionAbstract';
import EventManager from 'System/Event/EventManager';

import CreateElementByEvent from 'System/Helper/CreateElementByEvent';
import CreateElement from 'System/Helper/CreateElement';
import ObjectToArray from 'System/Object/ObjectToArray';

import ChildProjectionDirection from 'Admin/Editor/Projection/ChildProjectionDirection';
import BaseComponent from 'Admin/Editor/Component/BaseComponent';

export default class EditorProjection extends LayoutProjectionAbstract
{
    public get columns(): any
    {
        return this.entityOptions.get('columns');
    }

    public set columns(value: any)
    {
        this.entityOptions.set('columns', value);
    }

    public initialProjection(): void
    {
        super.initialProjection();

        if (!Array.isArray(this.columns))
        {
            this.columns = ObjectToArray(this.columns);
        }

        for (let i = 0; i < this.columns.length; i++)
        {
            if (!Array.isArray(this.columns[i].entities))
            {
                this.columns[i].entities = ObjectToArray(this.columns[i].entities);
            }

            if (Array.isArray(this.columns[i].options))
            {
                this.columns[i].options = Object.assign({}, this.columns[i].options);
            }

            this.createColumnElement(i);
        }
    }

    public attachEvents(manager: EventManager): void
    {
        super.attachEvents(manager);

        manager.addSystemHandler(
            this.moveChildProjectionEvent,
            this.onMoveChildProjection
        );

        this.addElementHandler(
            'click',
            `.actions-wrapper.entity-id-${this.editorComponent.entityId} .add-column-action`,
            this.onAddColumnClick
        );

        this.addElementHandler(
            'click',
            `.column-item .actions.entity-id-${this.editorComponent.entityId} .remove-action`,
            this.onRemoveColumnClick
        );

        this.addElementHandler(
            'click',
            `.column-item .actions.entity-id-${this.editorComponent.entityId} .edit-action`,
            this.onEditColumnClick
        );
    }

    protected onEditColumnClick(event: Event): void
    {
        let editButton = CreateElementByEvent(event),
            columnItem = editButton.parents('.column-item:first'),
            columnIndex = columnItem.data('column-index');

        this.panelManager.openPanel(
            'ColumnsLayoutItem',
            {
                columnIndex: columnIndex
            },
            this.editorComponent.entityId
        );
    }

    protected onRemoveColumnClick(event: Event): void
    {
        let removeButton = CreateElementByEvent(event),
            columnItem = removeButton.parents('.column-item:first'),
            columnIndex = columnItem.data('column-index');

        this.removeColumn(columnIndex);
    }

    protected onAddColumnClick(event: Event): void
    {
        let columnIndex = this.createEmptyColumn();

        this.panelManager.openPanel(
            'ColumnsLayoutItem',
            {
                columnIndex: columnIndex
            },
            this.editorComponent.entityId
        );
    }

    protected onMoveChildProjection(args: any): void
    {
        if (!args.editorComponent.hasOwnProperty('projection'))
        {
            return;
        }

        if (args.direction == ChildProjectionDirection.same)
        {
            this.outsideChild(args.editorComponent);

            this.insideChild(args.editorComponent);
        }
        else if (args.direction == ChildProjectionDirection.inside)
        {
            this.insideChild(args.editorComponent);
        }
        else if (args.direction == ChildProjectionDirection.outside)
        {
            this.outsideChild(args.editorComponent);
        }
    }

    protected insideChild(editorComponent: BaseComponent)
    {
        let moveProjectionElement = (editorComponent as any).projection.componentElement,
            columnSelector = `.column-item.entity-id-${this.editorComponent.entityId}`,
            columnElement = moveProjectionElement.parents(columnSelector),
            newColumnIndex = columnElement.data('column-index');

        let columns = this.columns;

        columns[newColumnIndex].entities.push(editorComponent.entityId);

        this.columns = columns;
    }

    protected outsideChild(editorComponent: BaseComponent)
    {
        let oldColumnIndex = 0,
            columns = this.columns;

        for (let i in columns)
        {
            let column = columns[i];

            for (let j of column.entities)
            {
                if (j == editorComponent.entityId)
                {
                    oldColumnIndex = i as any;

                    break;
                }
            }
        }

        columns[oldColumnIndex].entities = columns[oldColumnIndex].entities.filter((entityId: any) => {
            return entityId != editorComponent.entityId;
        });

        this.columns = columns;
    }

    public insertChildComponentElement(domElement: JQuery, editorComponent: BaseComponent): void
    {
        let columns = this.columns,
            indexColumn = 0;

        for (let i in columns)
        {
            let column = columns[i];

            for (let j of column.entities)
            {
                if (j == editorComponent.entityId)
                {
                    indexColumn = i as any;

                    break;
                }
            }
        }

        this.getColumnContentElement(indexColumn).append(domElement);
    }

    public createEmptyColumn(): number
    {
        let columns = this.columns;

        columns.push({
            entities: [],
            options: {}
        });

        this.columns = columns;

        let index = columns.length - 1;

        this.createColumnElement(index);

        return index;
    }

    public createColumnElement(index: number): JQuery
    {
        let columnElement = CreateElement(`
            <div class="column-item column-item-${index} entity-id-${this.editorComponent.entityId}"
                data-column-index="${index}">
                <div class="column-header">
                    <div class="column-title">
                        <span class="text">${this.localizedMessages.columnsLayout.column} ${index}</span>
                    </div>
                    <div class="actions entity-id-${this.editorComponent.entityId}">
                        <div class="button-link edit-action">
                            <span class="text">${this.localizedMessages.columnsLayout.edit}</span>
                        </div>
                        <div class="button-link remove-action">
                            <span class="text">${this.localizedMessages.columnsLayout.remove}</span>
                        </div>
                    </div>
                </div>
                <div class="sort-list column-content entity-id-${this.editorComponent.entityId}"></div>
            </div>
        `);

        this.componentElement.find('.body-container:first').append(columnElement);

        this.projectionManager.reloadSortable();

        return columnElement;
    }

    public removeColumn(index: number): void
    {
        if (index == 0)
        {
            return;
        }

        let columns = this.columns;

        if (columns.length > index && 0 <= index)
        {
            let entitiesInColumn = columns[index].entities;

            columns.splice(index, 1);

            this.columns = columns;

            for (let entityId of entitiesInColumn)
            {
                if (!this.register.components.has(entityId))
                {
                    continue;
                }

                this.projectionManager.moveProjection(
                    this.register.components.get(entityId),
                    this.editorComponent
                );
            }

            this.removeColumnElement(index);

            for (let i = index + 1; i <= columns.length; i++)
            {
                let newIndex = i - 1,
                    columnItem = this.getColumnElement(i);

                columnItem.removeClass(`column-item-${i}`)
                    .addClass(`column-item-${newIndex}`)
                    .attr('data-column-index', newIndex);

                columnItem.find('> .column-header .column-title .text')
                    .html(this.localizedMessages.columnsLayout.column + ' ' + newIndex);
            }
        }
    }

    public removeColumnElement(index: number): void
    {
        this.getColumnElement(index).remove();

        this.projectionManager.reloadSortable();
    }

    protected getColumnElement(index: number): JQuery
    {
        return this.componentElement.find(
            `.column-item.column-item-${index}.entity-id-${this.editorComponent.entityId}`
        );
    }

    protected getColumnContentElement(index: number): JQuery
    {
        return this.componentElement.find(
            `.column-item.column-item-${index} .column-content.entity-id-${this.editorComponent.entityId}`
        );
    }
}


