import FieldListPanel from 'Admin/Field/Editor/FieldListPanel';

import ExtendObject from 'System/Object/ExtendObject';

export default class EditorPanel extends FieldListPanel
{
    public getValidatorOptions(): object
    {
        let options: any = super.getValidatorOptions();

        options.rules = ExtendObject(options.rules, {
            colors: {
                repeaterItem: {
                    type: {
                        required: true
                    },
                    term: {
                        required: true
                    },
                    markerStyle: {
                        required: true
                    },
                    color: {
                        requiredIfActiveControl: true
                    }
                }
            }
        });

        return options;
    }
}


