import FieldComponent from 'Admin/Field/Editor/FieldComponent';
import IFilteringComponent from 'Admin/Editor/Component/IFilteringComponent';

export default class FieldListComponent extends FieldComponent implements IFilteringComponent
{
    public getFilterKeys(): string[]
    {
        return [this.options.get('optionKey')];
    }
}


