import ComponentAbstract from 'Admin/Component/ComponentAbstract';
import EventManager from 'System/Event/EventManager';

import PanelLayoutAbstract from 'Admin/Editor/PanelLayout/PanelLayoutAbstract';

export default class Builder extends ComponentAbstract
{
    public initialProperties(): void
    {
        this.app.componentRegister.set('Editor/Builder/PanelLayout', this);
    }

    public attachEvents(manager: EventManager): void {}

    public createPanelLayout(key: string): PanelLayoutAbstract
    {
        if (!this.register.panelStructures.has(key))
        {
            throw new Error(`Panel structure with id '${key}' not found`);
        }

        let panelStructure = this.register.panelStructures.get(key),
            panelLayoutPrototype = this.prototypes.panelLayout.get(panelStructure.panelLayoutKey),
            panelLayout = this.componentBuilder.build(panelLayoutPrototype);

        panelLayout.panelStructure = panelStructure;

        return panelLayout;
    }
}


