import ElementComponent from 'Admin/Component/ElementComponent';
import ApplicationEvent from 'System/ApplicationEvent';
import EventManager from 'System/Event/EventManager';

import Context from 'System/Object/Context';

import ObjectRepository from 'App/Option/ObjectRepository';

import PanelLayoutAbstract from 'Admin/Editor/PanelLayout/PanelLayoutAbstract';
import PanelStatus from 'Admin/Editor/Panel/PanelStatus';
import IStatusChangedEventArgs from 'Admin/Editor/Panel/EventArgs/IStatusChangedEventArgs';

export default class BasePanel extends ElementComponent
{
    public panelKey: string;

    public panelContext: any;

    protected optionsContainer: object;

    protected optionsRepository: ObjectRepository;

    public panelLayout: PanelLayoutAbstract;

    protected statusProp: PanelStatus;

    public statusChangedEvent: ApplicationEvent<IStatusChangedEventArgs>;

    public constructor()
    {
        super();

        this.optionsContainer = {};

        this.statusProp = PanelStatus.None;

        this.statusChangedEvent = new ApplicationEvent<IStatusChangedEventArgs>();
    }

    public get options(): ObjectRepository
    {
        return this.optionsRepository;
    }

    public get status(): PanelStatus
    {
        return this.statusProp;
    }

    public set status(value: PanelStatus)
    {
        let oldStatus = this.statusProp;

        this.statusProp = value;

        if (oldStatus != value)
        {
            this.statusChangedEvent.trigger({
                panel: this,
                oldStatus: oldStatus,
                status: this.statusProp
            });
        }
    }

    public get isControlsSaved(): boolean
    {
        return this.panelLayout.isControlsSaved;
    }

    public set isControlsSaved(value: boolean)
    {
        this.panelLayout.isControlsSaved = value;
    }

    public get formElement(): JQuery
    {
        return this.componentElement.find('form.panel-form');
    }

    public initialProperties(): void
    {
        this.optionsRepository = this.createOptionsRepository();
    }

    protected createOptionsRepository(): ObjectRepository
    {
        return new ObjectRepository(this.optionsContainer);
    }

    public attachEvents(manager: EventManager): void
    {
        manager.addSystemHandler(this.statusChangedEvent, this.onStatusChanged);

        if (!this.panelLayout.panelAutoSave)
        {
            manager.addSystemHandler(
                this.panelLayout.isControlsSavedChangedEvent,
                this.onIsControlsSavedChanged
            );

            this.addElementHandler(
                'click',
                '.panel-navigation .reset-panel-button',
                this.resetValuesOfControls
            );

            this.addElementHandler(
                'click',
                '.panel-navigation .apply-panel-button',
                this.saveValuesOfControls
            );
        }

        manager.addElementHandler(this.formElement, 'submit', this.onFormSubmit);
    }

    protected onFormSubmit(event: Event): void
    {
        event.preventDefault();
    }

    protected onStatusChanged(args: IStatusChangedEventArgs): void
    {
        let handlers = new Map<PanelStatus, string>([
            [PanelStatus.Active, 'onActive'],
            [PanelStatus.Inactive, 'onInactive'],
            [PanelStatus.Launched, 'onLaunched'],
            [PanelStatus.Destroyed, 'onDestroyed']
        ]);

        if (handlers.has(args.status) && handlers.get(args.status) in args.panel)
        {
            let func = (args.panel as any)[handlers.get(args.status)];

            func = Context(func, this);

            func();
        }
    }

    protected onIsControlsSavedChanged(): void
    {
        let resetButton = this.componentElement.find('.panel-navigation .reset-panel-button'),
            applyButton = this.componentElement.find('.panel-navigation .apply-panel-button');

        if (this.panelLayout.isControlsSaved)
        {
            resetButton.addClass('disabled');

            applyButton.addClass('disabled');
        }
        else
        {
            resetButton.removeClass('disabled');

            applyButton.removeClass('disabled');
        }
    }

    public saveValuesOfControls(): void
    {
        if (this.panelLayout.isControlsSaved)
        {
            return;
        }

        for (let control of this.panelLayout.controls.values())
        {
            control.saveOptionValue();
        }

        this.panelLayout.isControlsSaved = true;
    }

    public resetValuesOfControls(): void
    {
        if (this.panelLayout.isControlsSaved)
        {
            return;
        }

        for (let control of this.panelLayout.controls.values())
        {
            control.restoreOptionValue();
        }

        this.panelLayout.isControlsSaved = true;
    }

    public getTemplateContext(panelContext: object): object
    {
        return {};
    }
}


