import SelectiveComponent from 'Admin/Component/SelectiveComponent';
import EventManager from 'System/Event/EventManager';

import ScrollPage from 'System/Helper/ScrollPage';
import CreateElementByEvent from 'System/Helper/CreateElementByEvent';

import EntityStatus from 'App/Entity/EntityStatus';

export default class Dispatcher extends SelectiveComponent
{
    public rootSelector: string = '.woocommerce-product-filter-manage-project-wrapper';

    protected rowModeWidth: number = 1140;

    public initialProperties(): void
    {
        if (jQuery.hasOwnProperty('blockUI'))
        {
            jQuery.blockUI.defaults.message = null;
        }
    }

    public attachEvents(manager: EventManager): void
    {
        manager.addWindowHandler('load', this.onWindowLoaded);

        manager.addComponentHandler(
            'click',
            '.editor-panel .navigation .save-project',
            this.saveProject
        );

        manager.addComponentHandler(
            'click',
            '.editor-panel .navigation .add-element',
            this.onAddElementClick
        );

        manager.addComponentHandler(
            'click',
            '.panel.projections-panel .view-mode-toggle .view-mode',
            this.onViewModeClick
        );

        manager.addWindowHandler(
            'resize',
            this.onWindowResize
        );
    }

    protected onViewModeClick(event: Event): void
    {
        let modeElement = CreateElementByEvent(event),
            mode = modeElement.data('mode');

        this.setMode(mode);
    }

    public setMode(mode: string): void
    {
        this.rootElement.find('.panel.editor-panel').attr('data-panel-mode', mode);

        this.rootElement.find('.panel.projections-panel .view-mode-toggle .view-mode.active')
            .removeClass('active');

        this.rootElement.find(`.panel.projections-panel .view-mode-toggle .view-mode[data-mode="${mode}"]`)
            .addClass('active');
    }

    public getMode(): string
    {
        let mode = this.rootElement.find('.panel.editor-panel').attr('data-panel-mode');

        return mode ? mode : (window.innerWidth < this.rowModeWidth ? 'row' : 'column');
    }

    protected onWindowResize(event: Event): void
    {
        if (window.innerWidth < this.rowModeWidth)
        {
            this.setMode('row');
        }
    }

    protected onWindowLoaded(event: Event): void
    {
        if (!window.hasOwnProperty('ProductFilterData'))
        {
            return;
        }

        if (window.innerWidth < this.rowModeWidth)
        {
            this.setMode('row');
        }
        else
        {
            this.setMode('column');
        }

        let editorData = (window as any).ProductFilterData,
            editorComponentBuilder = this.builders.component,
            projectEntity = null;

        if (editorData.pageAction != 'edit' && editorData.pageAction != 'new')
        {
            return;
        }

        this.app.objectContainer.set('PageLinks', {
            listProject: editorData.pageLinks.list_project,
            newProject: editorData.pageLinks.new_project,
            editProject: editorData.pageLinks.edit_project,
        });

        this.app.objectContainer.set('Messages', editorData.messages);

        for (let panelId in editorData.editor.panels)
        {
            let structure = editorData.editor.panels[panelId];

            this.register.panelStructures.set(panelId, structure);
        }

        for (let entryIndex in editorData.registerEntities)
        {
            let entryData = editorData.registerEntities[entryIndex];

            this.register.entityTypes.set(entryIndex, {
                id: entryData.id,
                label: entryData.label,
                isGrouped: entryData.is_grouped,
                defaultOptions: entryData.default_options
            });
        }

        this.editorPackage.initEditorEvent.trigger(this.prototypes);

        // load project if exists or create virtual project
        if ((window as any).hasOwnProperty('ProductFilterProjectData'))
        {
            let projectStructure = (window as any).ProductFilterProjectData;

            projectEntity = this.entityCollector.collectEntityByStructure(projectStructure);
        }
        else
        {
            projectEntity = this.entityCollector.collectVirtualEntityByKey('Project');
        }

        if (editorData.pageAction == 'new' && editorData.postId)
        {
            projectEntity.entityId = editorData.postId;

            projectEntity.status = EntityStatus.published;
        }

        let projectEditorComponent = this.builders.component.createComponentByEntity(projectEntity);

        this.app.objectContainer.set('Editor/ProjectEntity', projectEntity);

        this.app.objectContainer.set('Editor/ProjectComponent', projectEditorComponent);

        this.editorPackage.projectLoadedEvent.trigger(this.register);

        // open default panel - project panel
        this.panelManager.openPanel(
            projectEntity.entityKey,
            projectEntity.entityId
        );

        if (this.panelManager.activePanels.length)
        {
            let projectPanel = this.panelManager.activePanels[0];

            this.panelManager.checkCorrectnessOfOptions(projectPanel);
        }

        if (editorData.pageAction == 'new')
        {
            projectEditorComponent.titleChangedEvent.trigger({
                title: projectEditorComponent.entityTitle,
                oldTitle: projectEditorComponent.entityTitle,
                entity: projectEditorComponent.entity,
                editorComponent: projectEditorComponent
            });
        }
    }

    public saveProject(): void
    {
        let contentElement = this.app.bodyElement.find('#wpcontent'),
            activePanels = this.panelManager.activePanels,
            noticeContainerElement = this.app.bodyElement.find('.wcpf-notices-container');

        if (activePanels.length)
        {
            for (let panel of activePanels)
            {
                if (!this.panelManager.checkCorrectnessOfOptions(panel))
                {
                    return;
                }

                this.panelManager.modalToSaveOptions(panel);

                if (!this.panelManager.checkCorrectnessOfOptions(panel))
                {
                    return;
                }
            }
        }

        contentElement.block();

        wp.ajax.send('wcpf_save_project', {
            type: 'POST',
            dataType: 'json',
            data: {
                projectEntity: JSON.stringify(
                    this.projectEntity
                )
            }
        }).fail((data) => {
            noticeContainerElement.html('');

            if (data.hasOwnProperty('messages') && data.messages.length)
            {
                for (let message of data.messages)
                {
                    noticeContainerElement.append(
                        this.getMessageTemplate(message)
                    );
                }

                ScrollPage(
                    noticeContainerElement.offset().top - (window.innerHeight / 2),
                    400
                );
            }
        }).done((data) => {
            noticeContainerElement.html('');

            if (data.hasOwnProperty('projectId'))
            {
                let pageLinks = this.app.objectContainer.get('PageLinks'),
                    editLink = pageLinks.editProject;

                editLink = editLink.replace(/\%project_id\%/g, data.projectId);

                location.href = editLink;
            }
        }).always(() => {
            contentElement.unblock();
        });
    }

    protected onAddElementClick(): void
    {
        this.panelManager.openPanel('AddElement');
    }

    protected getMessageTemplate(message: any): string
    {
        return `<div class="${message.level} ${message.level}-level-message">
                <p class="text">${message.text}</p>
            </div>`;
    }
}


