import SourceAbstract from 'Admin/Editor/Control/Source/SourceAbstract';
import CloneVariable from 'System/Object/CloneVariable';

export default class OptionSource extends SourceAbstract
{
    public saveValue(): void
    {
        let option = this.optionValue;

        if (typeof option == 'object')
        {
            option = CloneVariable(option);
        }

        this.panel.options.set(this.optionKey, option);
    }

    public getValue(): any
    {
        let option = this.panel.options.has(this.optionKey) ? CloneVariable(this.panel.options.get(this.optionKey)): undefined;

        if (typeof option == 'object')
        {
            option = CloneVariable(option);
        }

        return option;
    }
}


