import ComponentAbstract from 'Admin/Component/ComponentAbstract';
import EventManager from 'System/Event/EventManager';

import Compare from 'System/Object/Compare';
import ExtendObject from 'System/Object/ExtendObject';

export default class DefaultRules extends ComponentAbstract
{
    public attachEvents(manager: EventManager): void
    {
        manager.addSystemHandler(
            this.editorPackage.initEditorEvent,
            this.initEditor
        );
    }

    protected initEditor(prototypes: any): void
    {
        let container = this.register.controlRulesOperations;

        container.set('==', this.isEqualToOperator);

        container.set('!=', this.isNotEqualToOperator);

        container.set('>', this.greaterThanOperator);

        container.set('<', this.lessThanOperator);

        container.set('>=', this.greaterThanOrEqualToOperator);

        container.set('<=', this.lessThanOrEqualToOperator);

        container.set('in', this.inOperator);

        container.set('inControl', this.inControlOperator);

        container.set('notIn', this.notInOperator);

        container.set('notInControl', this.notInControlOperator);
    }

    protected isEqualToOperator(controlValue: any, rule: any): boolean
    {
        if (Array.isArray(controlValue))
        {
            controlValue = ExtendObject({}, controlValue);

            return Compare(controlValue, rule.value);
        }

        return controlValue == rule.value;
    }

    protected isNotEqualToOperator(controlValue: any, rule: any): boolean
    {
        return !this.isEqualToOperator(controlValue, rule);
    }

    protected greaterThanOperator(controlValue: any, rule: any): boolean
    {
        return controlValue > rule.value;
    }

    protected lessThanOperator(controlValue: any, rule: any): boolean
    {
        return !this.greaterThanOperator(controlValue, rule);
    }

    protected greaterThanOrEqualToOperator(controlValue: any, rule: any): boolean
    {
        return controlValue >= rule.value;
    }

    protected lessThanOrEqualToOperator(controlValue: any, rule: any): boolean
    {
        return controlValue <= rule.value;
    }

    protected inOperator(controlValue: any, rule: any): boolean
    {
        return rule.value.indexOf(controlValue) !== -1;
    }

    protected inControlOperator(controlValue: any, rule: any): boolean
    {
        if (Array.isArray(controlValue))
        {
            return controlValue.indexOf(rule.value) !== -1;
        }
        else if (typeof controlValue == 'object' && 'hasOwnProperty' in controlValue)
        {
            return controlValue.hasOwnProperty(rule.value);
        }

        return false;
    }

    protected notInOperator(controlValue: any, rule: any): boolean
    {
        return !this.inOperator(controlValue, rule);
    }

    protected notInControlOperator(controlValue: any, rule: any): boolean
    {
        return !this.inControlOperator(controlValue, rule);
    }
}


