import Entity from 'App/Entity/Entity';

export default function FindEntityById(id: any, root: Entity): Entity
{
    if (root.entityId == id)
    {
        return root;
    }

    let result = null,
        groupedList = [];


    for (let i in root.childEntities)
    {
        let item = root.childEntities[i];

        if (item.childEntities.length)
        {
            groupedList.push(item);
        }

        if (item.entityId == id)
        {
            result = item;

            break;
        }
    }

    if (result == null)
    {
        for (let i in groupedList)
        {
            let item = groupedList[i],
                itemResult = FindEntityById(id, item);

            if (itemResult instanceof Entity)
            {
                result = itemResult;

                break;
            }
        }
    }

    return result;
}


