import EventManager from 'System/Event/EventManager';
import BaseControl from 'Admin/Editor/Control/BaseControl';
import ReloadControl from 'Admin/Editor/Control/Decorators/ReloadControl';
import IReloadControl from 'Admin/Editor/Control/IReloadControl';

import Context from 'System/Object/Context';
import CloneVariable from 'System/Object/CloneVariable';

@ReloadControl
export default class ColorListControl extends BaseControl implements IReloadControl
{
    protected isRestoredOption: boolean = false;

    protected mediaFrame: any;

    protected images: any = {};

    protected attachmentQuery: any = {};

    public get controlLoader(): any
    {
        return this.app.componentRegister.get('Editor/Control/ControlLoader');
    }

    public initialProperties(): void
    {
        super.initialProperties();

        this.mediaFrame = wp.media({
            multiple: false,
            library: {
                type: 'image'
            }
        });

        this.mediaFrame.on('select', Context(this.onSelectImage, this));
    }

    public getEmptyValue(): any
    {
        return {};
    }

    public restoreOptionValue(): void
    {
        super.restoreOptionValue();

        let taxonomiesColors = (this.controlStructure as any).taxonomiesColors;

        if (Array.isArray(this.optionValue) || typeof this.optionValue != 'object') {
            if (taxonomiesColors.hasOwnProperty(this.optionKey)) {
                this.optionValueProp = CloneVariable(taxonomiesColors[this.optionKey]);
            } else {
                this.optionValueProp = {};

                return;
            }
        } else if (!Array.isArray(this.optionValue)
            && typeof this.optionValue == 'object'
            && taxonomiesColors.hasOwnProperty(this.optionKey)) {
            let baseColors = CloneVariable(taxonomiesColors[this.optionKey]);

            this.optionValueProp = jQuery.extend(baseColors, this.optionValueProp);
        }

        this.isRestoredOption = true;

        for (let term in this.optionValue) {
            let colorOption = this.optionValue[term],
                colorElement = this.componentElement.find(`.color-item[data-term=${term}]`);

            if (colorOption.image && colorElement.find('.image-element').attr('src')) {
                this.images[colorOption.image] = colorElement.find('.image-element').attr('src');
            }

            this.changeMarkerStyleSwitch(colorOption.term, colorOption.markerStyle);

            this.changeTypeSwitch(colorOption.term, colorOption.type);

            this.changeBorderColorElement(colorOption.term, colorOption.borderColor, true);

            this.changeColorElement(colorOption.term, colorOption.color, true);

            this.changeImageElement(colorOption.term, colorOption.image);
        }

        this.isRestoredOption = false;
    }

    protected onSelectImage(): void
    {
        let term = this.mediaFrame.$el.data('term'),
            attachment = this.mediaFrame.state().get('selection').first().toJSON();

        this.images[attachment.id] = attachment.url;

        this.changeImageElement(term, attachment.id);
    }

    protected changeImageElement(term, value: any): void
    {
        let colorElement = this.componentElement.find(`.color-item[data-term=${term}]`),
            imageContainer = colorElement.find('.image-container');

        if (this.attachmentQuery.hasOwnProperty(term) && this.attachmentQuery[term]) {
            this.attachmentQuery[term].abort();
        }

        this.changeColorOption(term, 'image', value);

        imageContainer.find('.image-element').attr('src', '');

        if (value && !this.images.hasOwnProperty(value)) {
            let attachmentEntity = wp.media.attachment(value);

            this.attachmentQuery[term] = attachmentEntity.sync('read');

            this.attachmentQuery[term].done((data) => {
                this.images[value] = data.url;

                imageContainer.find('.image-element').attr('src', data.url);
            }).always(() => {
                this.updatePreviewColor(term);
            });
        } else if (value && this.images.hasOwnProperty(value)) {
            imageContainer.find('.image-element').attr('src', this.images[value]);
        }

        this.updatePreviewColor(term);
    }

    protected changeColorElement(term, value: any, updateColorPicker: boolean = false): void
    {
        let colorElement = this.componentElement.find(`.color-item[data-term=${term}]`),
            colorInputElement = colorElement.find('.color-picker-element'),
            previewElement = colorElement.find('.preview-color');

        if (updateColorPicker) {
            colorInputElement.wpColorPicker('color', value);

            if (!value || !value.length) {
                let pickerContainer = colorInputElement.parents('.wp-picker-container');

                pickerContainer.find('.wp-color-result').css('background-color', '');
            }
        }

        if (this.optionValue[term].type == 'color')
        {
            previewElement.css('background', value);
        }

        this.changeColorOption(term, 'color', value);
    }

    protected changeBorderColorElement(term, value: any, updateColorPicker: boolean = false): void
    {
        let colorElement = this.componentElement.find(`.color-item[data-term=${term}]`),
            colorInputElement = colorElement.find('.border-picker-element'),
            previewElement = colorElement.find('.preview-color');

        if (updateColorPicker) {
            colorInputElement.wpColorPicker('color', value);

            if (!value || !value.length) {
                let pickerContainer = colorInputElement.parents('.wp-picker-container');

                pickerContainer.find('.wp-color-result').css('background-color', '');
            }
        }

        previewElement.css('border-color', value);

        this.changeColorOption(term, 'borderColor', value);
    }

    protected changeMarkerStyleSwitch(term, value: string): void
    {
        let colorElement = this.componentElement.find(`.color-item[data-term=${term}]`),
            switchElement = colorElement.find('.switch-element[data-name="marker"]');

        this.changeSwitchElement(switchElement, value);

        this.changeColorOption(term, 'markerStyle', value);
    }

    protected changeTypeSwitch(term, value: string): void
    {
        let colorElement = this.componentElement.find(`.color-item[data-term=${term}]`),
            switchElement = colorElement.find('.switch-element[data-name="type"]');

        this.changeSwitchElement(switchElement, value);

        if (value == 'color') {
            colorElement.find('.image-container').hide();

            colorElement.find('.color-picker-container').show();
        } else if (value == 'image') {
            colorElement.find('.image-container').show();

            colorElement.find('.color-picker-container').hide();
        }

        this.changeColorOption(term, 'type', value);

        this.updatePreviewColor(term);
    }

    protected updatePreviewColor(term): void
    {
        let colorElement = this.componentElement.find(`.color-item[data-term=${term}]`),
            previewElement = colorElement.find('.preview-color'),
            imageId = this.optionValue[term].image;

        if (this.optionValue[term].type == 'color') {
            previewElement.css('background-image', '');

            previewElement.css('background', this.optionValue[term].color);
        } else if (this.optionValue[term].type == 'image') {
            let previewImage = colorElement.find('.image-container .preview-image-container'),
                uploadImage = colorElement.find('.image-container .upload-container');

            previewElement.css('background', '');

            if (imageId && this.images.hasOwnProperty(imageId)) {
                previewElement.css('background-image', `url(${this.images[imageId]})`);

                previewImage.removeClass('hidden');

                uploadImage.addClass('hidden');
            } else {
                previewElement.css('background-image', '');

                previewImage.addClass('hidden');

                uploadImage.removeClass('hidden');
            }
        }
    }

    protected changeSwitchElement(switchElement, value): void
    {
        switchElement.find('.option.active')
            .removeClass('active');

        switchElement.find(`.option[data-value="${value}"]`)
            .addClass('active');
    }

    public reloadControl(props: any): void
    {
        this.componentElement.find('.control-content').html(
            props.controlElement.find('.control-content').html()
        );

        this.initialElementProperties();

        this.attachElementEvents(this.eventManager);
    }

    public afterReloadDependentOption(): void
    {
        this.restoreOptionValue();
    }

    protected initialElementProperties(): void
    {
        let colorHandler = (event) => {
            setTimeout(() => {
                let colorInput = jQuery(event.target),
                    colorItem = colorInput.parents('.color-item:first'),
                    color = colorInput.hasClass('wp-picker-clear') ? '' : colorInput.val();

                this.changeColorElement(colorItem.data('term'), color);
            }, 1);
        },  borderHandler = (event) => {
            setTimeout(() => {
                let colorInput = jQuery(event.target),
                    colorItem = colorInput.parents('.color-item:first'),
                    color = colorInput.hasClass('wp-picker-clear') ? '' : colorInput.val();

                this.changeBorderColorElement(colorItem.data('term'), color);
            }, 1);
        };

        this.componentElement.find('.color-picker-element').wpColorPicker({
            hide: true,
            palettes: true,
            change: colorHandler,
            clear: colorHandler
        });

        this.componentElement.find('.border-picker-element').wpColorPicker({
            hide: true,
            palettes: true,
            change: borderHandler,
            clear: borderHandler
        });
    }

    public attachEvents(manager: EventManager): void
    {
        super.attachEvents(manager);

        manager.addElementHandler(
            this.componentElement,
            'click',
            '.image-container .upload-container .upload-image',
            this.onUpdateImageClick
        );

        manager.addElementHandler(
            this.componentElement,
            'click',
            '.image-container .delete-image',
            this.onRemoveImageClick
        );

        manager.addElementHandler(
            this.componentElement,
            'mouseenter',
            '.switch-element[data-name="marker"] .option',
            this.onMarkerMouseEnter
        );

        manager.addElementHandler(
            this.componentElement,
            'mouseleave',
            '.switch-element[data-name="marker"] .option',
            this.onMouseLeaveEnter
        );
    }

    protected attachElementEvents(manager: EventManager): void
    {
        manager.addElementHandler(
            this.componentElement.find('.switch-element'),
            'click',
            '.option:not(.active)',
            this.onSwitchClick
        );
    }

    protected onMarkerMouseEnter(event): void
    {
        let optionElement = jQuery(event.currentTarget),
            colorItem = optionElement.parents('.color-item:first');

        colorItem.addClass('marker-preview')
            .addClass(`marker-preview-${optionElement.data('value')}`);
    }

    protected onMouseLeaveEnter(event): void
    {
        let optionElement = jQuery(event.currentTarget),
            colorItem = optionElement.parents('.color-item:first');

        colorItem.removeClass('marker-preview')
            .removeClass(`marker-preview-${optionElement.data('value')}`);
    }

    protected onRemoveImageClick(event): void
    {
        let buttonElement = jQuery(event.currentTarget),
            colorItem = buttonElement.parents('.color-item:first');

        this.changeImageElement(colorItem.data('term'), '');
    }

    protected onUpdateImageClick(event): void
    {
        let buttonElement = jQuery(event.currentTarget),
            colorItem = buttonElement.parents('.color-item:first');

        this.mediaFrame.open(colorItem);

        this.mediaFrame.$el.data('term', colorItem.data('term'));
    }

    protected onSwitchClick(event): void
    {
        let optionElement = jQuery(event.currentTarget),
            colorItem = optionElement.parents('.color-item:first'),
            switchName = optionElement.parents('.switch-element').data('name'),
            option = optionElement.data('value');

        if (switchName == 'type') {
            this.changeTypeSwitch(colorItem.data('term'), option);
        } else if (switchName == 'marker') {
            this.changeMarkerStyleSwitch(colorItem.data('term'), option);
        }
    }

    protected changeColorOption(term, optionName: string, optionValue: any): void
    {
        if (this.optionValue[term][optionName] == optionValue)
        {
            return;
        }

        let prevOption = CloneVariable(this.optionValue);

        this.optionValue[term][optionName] = optionValue;

        if (!this.isRestoredOption)
        {
            this.valueChangedEvent.trigger({
                oldValue: prevOption,
                value: this.optionValue,
                control: this
            });
        }
    }
}


