import EditorComponentPanel from 'Admin/Editor/Panel/EditorComponentPanel';

import CreateElement from 'System/Helper/CreateElement';

import ObjectRepository from 'App/Option/ObjectRepository';

export default class ColumnPanel extends EditorComponentPanel
{
    protected createOptionsRepository(): ObjectRepository
    {
        let entityOptions = this.editorComponent.entity.options,
            optionsContainer = entityOptions.columns[this.panelContext.columnIndex].options;

        return new ObjectRepository(optionsContainer);
    }

    public getValidatorOptions(): object
    {
        return {
            rules: {
                width: {
                    required: true,
                    number: true,
                    min: 1
                },
                widthUnit: {
                    requiredUnit: true
                }
            }
        };
    }
}


