import PackageAbstract from 'System/Package/PackageAbstract';
import ComponentAbstract from 'System/Component/ComponentAbstract';

import BaseControl from 'Admin/Editor/Control/BaseControl';
import SourceAbstract from 'Admin/Editor/Control/Source/SourceAbstract';

import SelectControl from 'Admin/Editor/Control/SelectControl';
import TextControl from 'Admin/Editor/Control/TextControl';
import RadioListControl from 'Admin/Editor/Control/RadioListControl';
import TextSizeControl from 'Admin/Editor/Control/TextSizeControl';
import SwitchControl from 'Admin/Editor/Control/SwitchControl';
import CheckListControl from 'Admin/Editor/Control/CheckListControl';
import RepeaterControl from 'Admin/Editor/Control/RepeaterControl';
import ColorPickerControl from 'Admin/Editor/Control/ColorPickerControl';
import ImageControl from 'Admin/Editor/Control/ImageControl';
import ColorListControl from 'Admin/Editor/Control/ColorListControl';
import RulesBuilderControl from 'Admin/Editor/Control/RulesBuilderControl';

import OptionControlSource from 'Admin/Editor/Control/Source/OptionSource';
import EntityControlSource from 'Admin/Editor/Control/Source/EntitySource';
import ContainerControlSource from 'Admin/Editor/Control/Source/ContainerSource';

import DefaultRules from 'Admin/Editor/Control/RulesOperation/DefaultRules';
import ControlLoader from 'Admin/Editor/Control/ControlLoader';
import IOperationFunc from 'Admin/Editor/Control/RulesOperation/IOperationFunc';

export default class Package extends PackageAbstract
{
    public initialProperties(): void
    {
        this.app.objectContainer.set(
            'Editor/Control/Prototypes',
            new Map<string, typeof BaseControl>([
                ['Select', SelectControl],
                ['Text', TextControl],
                ['RadioList', RadioListControl],
                ['TextSize', TextSizeControl],
                ['Switch', SwitchControl],
                ['CheckList', CheckListControl],
                ['Repeater', RepeaterControl],
                ['ColorPicker', ColorPickerControl],
                ['Image', ImageControl],
                ['ColorList', ColorListControl],
                ['RulesBuilder', RulesBuilderControl]
            ])
        );

        this.app.objectContainer.set(
            'Editor/Control/Source/Prototypes',
            new Map<string, typeof SourceAbstract>([
                ['options', OptionControlSource],
                ['entity', EntityControlSource],
                ['container', ContainerControlSource]
            ])
        );

        this.app.objectContainer.set(
            'Editor/Control/RulesOperation',
            new Map<string, IOperationFunc>()
        );
    }

    public getInitialComponents(): ComponentAbstract[]
    {
        return [
            new DefaultRules(),
            new ControlLoader()
        ];
    }
}


