import ComponentAbstract from 'System/Component/ComponentAbstract';
import ITreeEntity from 'System/Entity/ITreeEntity';
import HasProperty from 'System/Object/HasProperty';
import GetEntityElement from 'System/Helper/GetEntityElement';

abstract class SelectiveComponent extends ComponentAbstract implements ITreeEntity
{
    public rootSelector: string = '';

    public get rootElement(): JQuery
    {
        return GetEntityElement(this);
    }

    public getParentEntity(): ITreeEntity | null
    {
        if (HasProperty(this.package, 'rootSelector'))
        {
            return this.package as any;
        }

        return null;
    }

    public getEntityElement(endSelector: string): JQuery
    {
        return GetEntityElement(this, endSelector);
    }
}

export default SelectiveComponent;


