import ComponentAbstract from 'Admin/Component/ComponentAbstract';
import EventManager from 'System/Event/EventManager';

import CreateElement from 'System/Helper/CreateElement';
import Context from 'System/Object/Context';

export default class Bootstrap extends ComponentAbstract
{
    public get validator(): any
    {
        return jQuery.validator;
    }

    public attachEvents(manager: EventManager): void
    {
        manager.addSystemHandler(this.editorPackage.initEditorEvent, this.initEditor);
    }

    protected initEditor(): void
    {
        this.validator.setDefaults({
            debug: true,
            ignore: '.control-inactive,*:not([name])',
            errorPlacement: Context(this.errorPlacement, this)
        });

        let messages = {
            filterKeyText: '',
            filterKeyUniquer: '',
            requiredIfActiveControl: this.validator.messages.required
        };

        if (this.localizedMessages.hasOwnProperty('validatorErrors'))
        {
            for (let index in this.localizedMessages.validatorErrors)
            {
                let message = this.localizedMessages.validatorErrors[index];

                messages[index] = this.validator.format(message);
            }
        }

        this.validator.addMethod(
            'requiredIfActiveControl',
            Context(this.requiredIfActiveControlMethod, this),
            messages.requiredIfActiveControl
        );

        this.validator.addMethod(
            'requiredUnit',
            Context(this.requiredUnit, this),
            this.validator.messages.required
        );

        this.validator.addMethod(
            'filterKeyText',
            Context(this.filterKeyTextMethod, this),
            messages.filterKeyText
        );

        this.validator.addMethod(
            'filterKeyUniquer',
            Context(this.filterKeyUniquerMethod, this),
            messages.filterKeyUniquer
        );

        this.validator.addMethod(
            'filterKeyUniquerDepends',
            Context(this.filterKeyUniquerDependsMethod, this),
            messages.filterKeyUniquer
        );
    }

    protected requiredUnit(place, domElement): void
    {
        let element = CreateElement(domElement),
            form = element.parents('form.panel-form');

        let handler = Context(
            this.validator.methods.required,
            form.data('validator')
        );

        return handler(place, domElement, (unitDomElement) => {
            let unitElement = CreateElement(unitDomElement),
                containerElement = unitElement.parents('.control-inputs-container:first'),
                textElement = containerElement.find('.text-size');

            return (textElement.val() as any).length > 0;
        });
    }

    protected errorPlacement(place, element): void
    {
        let controlElement = element.parents('.control:first'),
            errorContainerElement = controlElement.find('.validation-errors-container:first');

        if (errorContainerElement.length)
        {
            errorContainerElement.append(place);
        }
        else
        {
            place.insertAfter(element.get(0));
        }
    }

    protected requiredIfActiveControlMethod(value, domElement, params): boolean
    {
        let element = CreateElement(domElement),
            control = element.parents('.control:first'),
            form = element.parents('form.panel-form');

        if (!control.hasClass('control-inactive') && form.data('validator'))
        {
            let handler = Context(
                this.validator.methods.required,
                form.data('validator')
            );

            return handler(value, domElement, params);
        }

        return true;
    }

    protected filterKeyTextMethod(value, element): boolean
    {
        let template = /^[\w\d0-9\-\_]*$/;

        return template.test(value);
    }

    protected filterKeyUniquerMethod(value, element, params): boolean
    {
        let panel = this.panelManager.getCurrentPanel(),
            entityInPanel = null;

        if (panel.hasOwnProperty('editorComponent'))
        {
            entityInPanel = (panel as any).editorComponent.entityId;
        }

        for (let [entityId, editorComponent] of this.register.components)
        {
            if (entityInPanel == editorComponent.entityId || editorComponent.entityStatus == 'remove')
            {
                continue;
            }

            if ('getFilterKeys' in editorComponent && (editorComponent as any).getFilterKeys().indexOf(value) != -1)
            {
                return false;
            }
        }

        return true;
    }

    protected filterKeyUniquerDependsMethod(value, element, params): boolean
    {
        let panel = this.panelManager.getCurrentPanel();

        if (Array.isArray(params))
        {
            for (let field of params)
            {
                let control = panel.panelLayout.controls.get(field);

                if (!control)
                {
                    continue;
                }

                if (control.optionValue == value)
                {
                    return false;
                }
            }
        }

        return true;
    }
}


