import EventManager from 'System/Event/EventManager';

import InputControlAbstract from 'Admin/Editor/Control/InputControlAbstract';

export default class TextSizeControl extends InputControlAbstract
{
    protected get unitSelectElement(): JQuery
    {
        return this.componentElement.find('.select-unit');
    }

    public attachEvents(manager: EventManager): void
    {
        super.attachEvents(manager);

        manager.addElementHandler(
            this.controlElement,
            'change paste keyup',
            this.updateValueFromElement
        );

        manager.addElementHandler(
            this.unitSelectElement,
            'change',
            this.updateValueFromElement
        );
    }

    protected updateValueFromElement(): void
    {
        let value = this.controlElement.val() as any;

        value = value.replace(/[^0-9\.]/g,'');

        this.controlElement.val(value);

        super.updateValueFromElement();
    }

    public setElementValue(value: any): void
    {
        if (typeof value == 'number')
        {
            value = value + '';
        }

        if (typeof value != 'string' || !value.length)
        {
            this.controlElement.val('');

            this.unitSelectElement.val('');
        }
        else
        {
            let parseValue = this.parseUnit(value);

            if ((typeof parseValue[0] == 'string' || typeof parseValue[0] == 'number') && !isNaN(parseValue[0]))
            {
                this.controlElement.val(parseValue[0]);
            }

            this.unitSelectElement.val(parseValue[1]);
        }
    }

    public getElementValue(): any
    {
        let size = this.controlElement.val() as any,
            unit = this.unitSelectElement.val() as any;

        if (size.length)
        {
            size = parseFloat(size);
        }

        return size + unit;
    }

    protected parseUnit(value: string): any
    {
        if (typeof value != 'string')
        {
            return [0, ''];
        }

        return [
            parseFloat(value),
            value.match(/[\d.\-\+]*\s*(.*)/)[1] || ''
        ];
    }
}


