import BaseComponent from 'Admin/Editor/Component/BaseComponent';
import EventManager from 'System/Event/EventManager';
import IHasPanels from 'Admin/Editor/Component/IHasPanels';

import BasePanel from 'Admin/Editor/Panel/BasePanel';

export default class EditorComponent extends BaseComponent implements IHasPanels
{
    public panels: Set<BasePanel> = new Set<BasePanel>();

    public attachEvents(manager: EventManager): void
    {
        super.attachEvents(manager);

        manager.addSystemHandler(
            this.titleChangedEvent,
            this.onTitleChanged
        );
    }

    protected onTitleChanged(args: any): void
    {
        let projectNameElement = this.app.bodyElement.find('.wp-heading-inline .project-name');

        projectNameElement.html(args.title);
    }
}


