import ElementComponent from 'Admin/Component/ElementComponent';
import EventManager from 'System/Event/EventManager';
import ApplicationEvent from 'System/ApplicationEvent';

import BaseControl from 'Admin/Editor/Control/BaseControl';
import IControlStructure from 'Admin/Editor/Control/IControlStructure';

import BasePanel from 'Admin/Editor/Panel/BasePanel';
import IPanelStructure from 'Admin/Editor/PanelLayout/IPanelStructure';

import IControlsSavedChangedEventArgs from 'Admin/Editor/PanelLayout/EventArgs/IControlsSavedChangedEventArgs';

abstract class PanelLayoutAbstract extends ElementComponent
{
    public panelStructure: IPanelStructure;

    public panel: BasePanel;

    public controls: Map<string, BaseControl>;

    protected isControlsSavedProp: boolean = true;

    public isControlsSavedChangedEvent: ApplicationEvent<IControlsSavedChangedEventArgs>;

    public constructor()
    {
        super();

        this.controls = new Map<string, BaseControl>();

        this.isControlsSavedChangedEvent = new ApplicationEvent<IControlsSavedChangedEventArgs>();
    }

    public get isControlsSaved(): boolean
    {
        return this.isControlsSavedProp;
    }

    public set isControlsSaved(value: boolean)
    {
        let oldValue = this.isControlsSavedProp;

        this.isControlsSavedProp = value;

        if (oldValue != value)
        {
            this.isControlsSavedChangedEvent.trigger({
                panelLayout: this,
                value: value
            });
        }
    }

    public get controlBuilder(): any
    {
        return this.app.componentRegister.get('Editor/Control/Builder');
    }

    public get panelAutoSave(): boolean
    {
        return this.panelStructure.panelAutoSave;
    }

    public get panelId(): string
    {
        return this.panelStructure.panelId;
    }

    public get panelTitle(): string
    {
        return this.panelStructure.panelTitle;
    }

    public get controlElements(): JQuery
    {
        return this.componentElement.find('.control[data-control-type]');
    }

    protected createControl(structure: IControlStructure): BaseControl
    {
        let control = this.builders.control.createControl(structure, this),
            controlElement = this.getControlElement(structure.controlKey, structure.optionKey);

        if (!controlElement.length)
        {
            throw new Error(`Element for option '${structure.optionKey}' not found`);
        }

        control.componentElement = controlElement;

        this.componentBuilder.implementation(control.controlSource, this.package);

        this.componentBuilder.implementation(control, this.package);

        this.eventManager.addSystemHandler(control.valueChangedEvent, this.onControlValueChanged);

        this.controls.set(control.optionKey, control);

        return control;
    }

    protected getControlElement(controlType: string, optionKey: string): JQuery
    {
        return this.controlElements.filter(`[data-control-type="${controlType}"][data-option-key="${optionKey}"]`);
    }

    protected onControlValueChanged(args: any): void
    {
        if (this.panelAutoSave)
        {
            args.control.controlSource.saveValue();

            this.isControlsSaved = true;
        }
        else
        {
            this.isControlsSaved = false;
        }
    }

    public attachEvents(manager: EventManager): void {}

    public abstract assembleControls(structure: IPanelStructure): void;
}

export default PanelLayoutAbstract;


