import ComponentAbstract from 'Admin/Component/ComponentAbstract';
import EventManager from 'System/Event/EventManager';

import FindEntityById from 'App/Entity/Helper/FindEntityById';
import Entity from 'App/Entity/Entity';

import BaseComponent from 'Admin/Editor/Component/BaseComponent';
import BaseProjection from 'Admin/Editor/Projection/BaseProjection';

export default class Builder extends ComponentAbstract
{
    public initialProperties(): void
    {
        this.app.componentRegister.set('Editor/Builder/Projection', this);
    }

    public attachEvents(manager: EventManager): void {}

    public createProjectionByEntityId(entityId: any): BaseProjection
    {
        let entity = FindEntityById(entityId, this.projectEntity);

        if (entity == null)
        {
            throw new Error(`Entity by id '${entityId}' not found`);
        }

        return this.createProjectionByEntity(entity);
    }

    public createProjectionByEntity(entity: Entity): BaseProjection
    {
        if (!this.register.components.has(entity.entityId))
        {
            throw new Error(`Element by entity id '${entity.entityId}' not found`);
        }

        return this.createProjectionByComponent(
            this.register.components.get(entity.entityId)
        );
    }

    public createProjectionByComponent(editorComponent: BaseComponent): BaseProjection
    {
        return this.createProjectionByKey(editorComponent.entityKey, editorComponent);
    }

    public createProjectionByKey(key: string, editorComponent?: BaseComponent): BaseProjection
    {
        if (!editorComponent.hasOwnProperty('projection'))
        {
            throw new Error(`Element '#${editorComponent.entityId} - ${editorComponent.entityTitle}' does not have projection support`);
        }

        if (!this.prototypes.projection.has(key))
        {
            throw new Error(`Projection controller prototype by key '${key}' not found`);
        }

        let prototype = this.prototypes.projection.get(key),
            projection = this.componentBuilder.build(prototype);

        if (editorComponent)
        {
            projection.editorComponent = editorComponent;

            (editorComponent as any).projection = projection;
        }

        projection.projectionKey = key;

        projection.package = this.package;

        return projection;
    }
}


