import ComponentAbstract from 'Admin/Component/ComponentAbstract';
import EventManager from 'System/Event/EventManager';

import LayoutComponent from 'Admin/Layout/Editor/LayoutComponent';
import EditorProjection from 'Admin/Layout/SimpleBox/EditorProjection';
import EditorPanel from 'Admin/Layout/SimpleBox/EditorPanel';

export default class Registration extends ComponentAbstract
{
    public attachEvents(manager: EventManager): void
    {
        manager.addSystemHandler(
            this.editorPackage.initEditorEvent,
            this.initEditor
        );
    }

    public initEditor(prototypes: any): void
    {
        prototypes.component.set('SimpleBoxLayout', LayoutComponent);

        prototypes.panel.set('SimpleBoxLayout', EditorPanel);

        prototypes.projection.set('SimpleBoxLayout', EditorProjection);
    }
}


