import EventManager from 'System/Event/EventManager';

import InputControlAbstract from 'Admin/Editor/Control/InputControlAbstract';

export default class RadioListControl extends InputControlAbstract
{
    public attachEvents(manager: EventManager): void
    {
        super.attachEvents(manager);

        manager.addPageElementHandler(
            'change',
            '.control-element',
            this.updateValueFromElement
        );
    }

    public getEmptyValue(): any
    {
        return [];
    }

    public setElementValue(value: any): void
    {
        let checkedElement = this.controlElement.filter(':checked');

        checkedElement.prop('checked', false);

        if (typeof value != 'string')
        {
            return;
        }

        let selectedElement = this.controlElement.filter(`[value="${value}"]`);

        selectedElement.prop('checked', true);
    }

    public getElementValue(): any
    {
        let checkedElement = this.controlElement.filter(':checked');

        if (checkedElement.length)
        {
            return checkedElement.val();
        }

        return undefined;
    }
}


