import BaseControl from 'Admin/Editor/Control/BaseControl';
import EventManager from 'System/Event/EventManager';

import Context from 'System/Object/Context';

export default class ImageControl extends BaseControl
{
    protected isAttached: boolean = false;

    protected attachmentQuery: any = null;

    protected mediaFrame: any;

    public initialProperties(): void
    {
        super.initialProperties();

        this.mediaFrame = wp.media({
            multiple: false,
            library: {
                type: 'image'
            }
        });
    }

    public attachEvents(manager: EventManager): void
    {
        super.attachEvents(manager);

        manager.addSystemHandler(
            this.valueChangedEvent,
            this.onValueChangedEvent
        );

        manager.addElementHandler(
            this.componentElement,
            'click',
            '.upload-container .upload-image',
            this.onUpdateButtonClick
        );

        manager.addElementHandler(
            this.componentElement,
            'click',
            '.image-container .delete-image',
            this.onRemoveButtonClick
        );

        this.mediaFrame.on('select', Context(this.onSelectImage, this));
    }

    protected onRemoveButtonClick(): void
    {
        this.optionValue = null;
    }

    protected onUpdateButtonClick(): void
    {
        this.mediaFrame.open();
    }

    protected onSelectImage(): void
    {
        let attachment = this.mediaFrame.state().get('selection').first().toJSON();

        this.optionValue = attachment.id;

        this.eventManager.removeSystemHandler(
            this.valueChangedEvent,
            this.onValueChangedEvent
        );

        this.updateOptionValue({
            id: attachment.id,
            url: attachment.url
        });

        this.eventManager.addSystemHandler(
            this.valueChangedEvent,
            this.onValueChangedEvent
        );
    }

    protected restoreDefaultOptionValue(needCheckValue?: boolean): void
    {
        super.restoreDefaultOptionValue(needCheckValue);

        if (!this.isAttached && this.optionValue)
        {
            this.updateOptionValue({
                id: this.optionValue
            });
        }
    }

    protected onValueChangedEvent(args: any): void
    {
        this.updateOptionValue({
            id: args.value
        });
    }

    public restoreOptionValue(): void
    {
        super.restoreOptionValue();

        this.updateOptionValue({
            id: this.optionValueProp
        });
    }

    protected updateOptionValue(props: any): void
    {
        let attachmentId = props.id,
            inputElement = this.componentElement.find('.control-input-element'),
            updateContainerElement = this.componentElement.find('.upload-container'),
            imageContainerElement = this.componentElement.find('.image-container');

        if (this.attachmentQuery)
        {
            this.attachmentQuery.abort();
        }

        if (!attachmentId)
        {
            inputElement.val('');

            this.updatePreviewImage('');

            updateContainerElement.removeClass('hidden');

            imageContainerElement.addClass('hidden');

            return;
        }
        else
        {
            updateContainerElement.addClass('hidden');

            imageContainerElement.removeClass('hidden');
        }

        if (props.hasOwnProperty('url'))
        {
            this.updatePreviewImage(props.url);
        }
        else
        {
            let attachmentEntity = wp.media.attachment(attachmentId);

            this.attachmentQuery = attachmentEntity.sync('read');

            this.attachmentQuery.done((data) => {
                this.updatePreviewImage(data.url);
            }).fail(() => {
                this.updatePreviewImage('');
            });
        }

        inputElement.val(attachmentId);
    }

    protected updatePreviewImage(imageUrl: string): void
    {
        this.componentElement.find('.image-element').attr('src', imageUrl);
    }
}


