import ElementComponent from 'Admin/Component/ElementComponent';
import EventManager from 'System/Event/EventManager';
import ApplicationEvent from 'System/ApplicationEvent';
import ArrayCompare from 'System/Object/ArrayCompare';
import ObjectCompare from 'System/Object/Compare';

import BasePanel from 'Admin/Editor/Panel/BasePanel';
import PanelStatus from 'Admin/Editor/Panel/PanelStatus';
import PanelLayoutAbstract from 'Admin/Editor/PanelLayout/PanelLayoutAbstract';

import IControlStructure from 'Admin/Editor/Control/IControlStructure';
import IValueChangedEventArgs from 'Admin/Editor/Control/EventArgs/IValueChangedEventArgs';
import SourceAbstract from 'Admin/Editor/Control/Source/SourceAbstract';

abstract class BaseControl extends ElementComponent
{
    public parentControl: BaseControl = null;

    public contextControls: Map<string, BaseControl> = new Map<string, BaseControl>();

    public panelLayout: PanelLayoutAbstract;

    public controlStructure: IControlStructure;

    public controlSource: SourceAbstract;

    protected optionValueProp: any;

    public valueChangedEvent: ApplicationEvent<IValueChangedEventArgs> = new ApplicationEvent<IValueChangedEventArgs>();

    public get panel(): BasePanel
    {
        return this.panelLayout.panel;
    }

    public get controlKey(): string
    {
        return this.controlStructure.controlKey;
    }

    public get controlLabel(): string
    {
        return this.controlStructure.controlLabel;
    }

    public get optionKey(): string
    {
        return this.controlStructure.optionKey;
    }

    public get optionValue()
    {
        return this.optionValueProp;
    }

    public set optionValue(value: any)
    {
        let oldValue = this.optionValueProp,
            isEqual = true;

        this.optionValueProp = value;

        if (typeof oldValue != typeof value)
        {
            isEqual = false;
        }
        else if (oldValue && Array.isArray(oldValue) && !ArrayCompare(oldValue, value))
        {
            isEqual = false;
        }
        else if (oldValue && !Array.isArray(oldValue) && typeof oldValue == 'object' && !ObjectCompare(oldValue, value))
        {
            isEqual = false;
        }
        else if (typeof oldValue != 'object' && oldValue != value)
        {
            isEqual = false;
        }

        if (!isEqual)
        {
            this.valueChangedEvent.trigger({
                oldValue: oldValue,
                value: value,
                control: this
            });
        }
    }

    public get isActiveControl(): boolean
    {
        return !this.componentElement.hasClass('control-inactive');
    }

    public set isActiveControl(isActive: boolean)
    {
        if (isActive)
        {
            this.componentElement.removeClass('control-inactive');

            this.showControlElement();
        }
        else
        {
            this.componentElement.addClass('control-inactive');

            this.hideControlElement();
        }
    }

    protected showControlElement(): void
    {
        this.componentElement.slideDown(400);
    }

    protected hideControlElement(): void
    {
        this.componentElement.slideUp(400);
    }

    public getEmptyValue(): any
    {
        return null;
    }

    public saveOptionValue(): void
    {
        this.controlSource.saveValue();
    }

    public restoreOptionValue(): void
    {
        this.optionValueProp = this.controlSource.getValue();
    }

    public initialProperties(): void
    {
        this.restoreOptionValue();

        if (this.panel.status != PanelStatus.None)
        {
            this.initializeDisplayRules();
        }
    }

    public attachEvents(manager: EventManager): void
    {
        manager.addSystemHandler(
            this.panel.statusChangedEvent,
            this.onPanelStatusChanged
        );
    }

    protected restoreDefaultOptionValue(needCheckValue?: boolean): void
    {
        if (this.controlStructure.defaultValue && ((needCheckValue && this.optionValue == undefined) || !needCheckValue))
        {
            this.optionValue = this.controlStructure.defaultValue;
        }
    }

    protected initializeDisplayRules(): void
    {
        if (typeof this.controlStructure.displayRules == 'object' && Object.keys(this.controlStructure.displayRules).length)
        {
            this.attachObserversAccordingToRules(
                this.controlStructure.displayRules
            );

            this.checkDisplayRules();
        }
        else
        {
            this.restoreDefaultOptionValue(true);
        }
    }

    protected onPanelStatusChanged(args: any): void
    {
        if (args.status == PanelStatus.Launched)
        {
            this.initializeDisplayRules();
        }
    }

    protected attachObserversAccordingToRules(rules: any): void
    {
        if ((rules.hasOwnProperty('relation') && rules.length - 1 === 0)
            || (!rules.hasOwnProperty('relation') && rules.length === 0))
        {
            return;
        }

        for (let i in rules)
        {
            let rule = rules[i];

            if (i == 'relation')
            {
                continue;
            }

            if (rule.hasOwnProperty('relation')
                || !rule.hasOwnProperty('optionKey'))
            {
                this.attachObserversAccordingToRules(rule);
            }
            else
            {
                let control = this.getControlByOptionKey(rule.optionKey);

                if (!control)
                {
                    continue;
                }

                this.eventManager.addSystemHandler(
                    control.valueChangedEvent,
                    this.checkDisplayRules
                );
            }
        }
    }

    protected checkDisplayRules(): void
    {
        this.isActiveControl = this.checkRules(this.controlStructure.displayRules);

        if (this.isActiveControl)
        {
            this.restoreDefaultOptionValue(true);
        }
    }

    protected checkRules(rules): boolean
    {
        let relation = 'AND',
            rulesLength = Object.keys(rules).length;

        if (rules.hasOwnProperty('relation'))
        {
            relation = rules.relation;

            rulesLength--;
        }

        if (!rulesLength)
        {
            return true;
        }

        for (let i in rules)
        {
            if (i == 'relation')
            {
                continue;
            }

            let rule = rules[i],
                resultOperation = null;

            if (rule.hasOwnProperty('relation')
                || !rule.hasOwnProperty('optionKey'))
            {
                resultOperation = this.checkRules(rule);
            }
            else
            {
                let control = this.getControlByOptionKey(rule.optionKey);

                if (!control)
                {
                    continue;
                }

                let controlValue = control.optionValue,
                    operation = rule.operation;

                if (!this.register.controlRulesOperations.has(operation))
                {
                    continue;
                }

                let operationHandler = this.register.controlRulesOperations.get(operation);

                resultOperation = operationHandler(controlValue, rule);
            }

            if (relation == 'AND' && !resultOperation)
            {
                return false;
            }
            else if (relation == 'OR' && resultOperation)
            {
                return true;
            }
        }

        return relation == 'AND';
    }

    public getControlByOptionKey(optionKey: string): BaseControl
    {
        if (this.contextControls.has(optionKey))
        {
            return this.contextControls.get(optionKey);
        }

        if (this.parentControl instanceof BaseControl)
        {
            let parent = this.parentControl,
                resultControl = null;

            while (parent)
            {
                resultControl = parent.getControlByOptionKey(optionKey);

                if (resultControl instanceof BaseControl)
                {
                    return resultControl;
                }

                if (parent.parentControl instanceof BaseControl)
                {
                    parent = parent.parentControl;
                }
                else
                {
                    break;
                }
            }
        }

        if (this.panelLayout.controls.has(optionKey))
        {
            return this.panelLayout.controls.get(optionKey);
        }

        return null;
    }
}

export default BaseControl;


