import PanelStatus from 'Admin/Editor/Panel/PanelStatus';

export default function ReloadControl(target: any): any
{
    return class extends target
    {
        protected needReloadControl: boolean = false;

        public get controlLoader(): any
        {
            return this.app.componentRegister.get('Editor/Control/ControlLoader');
        }

        public initialProperties(): void
        {
            super.initialProperties();

            if (this.panel.status != PanelStatus.None)
            {
                this.initializeReloadRules();
            }
        }

        protected checkDisplayRules(): void
        {
            super.checkDisplayRules();

            if (this.needReloadControl && this.isActiveControl)
            {
                this.controlLoader.reloadControl(this);

                this.needReloadControl = false;
            }
        }

        protected onPanelStatusChanged(args: any): void
        {
            super.onPanelStatusChanged(args);

            if (args.status == PanelStatus.Launched)
            {
                this.initializeReloadRules();
            }
        }

        protected initializeReloadRules(): void
        {
            let structure: any = this.controlStructure;

            if (structure.reloadAfterInit)
            {
                if (this.checkRules(this.controlStructure.displayRules))
                {
                    this.controlLoader.reloadControl(this, () => {
                        this.restoreOptionValue();
                    });
                }
                else
                {
                    this.needReloadControl = true;
                }
            }

            if (structure.optionsDepends)
            {
                for (let dependOption of structure.optionsDepends.values())
                {
                    let dependControl = this.getControlByOptionKey(dependOption);

                    if (!dependControl)
                    {
                        continue;
                    }

                    this.eventManager.addSystemHandler(dependControl.valueChangedEvent, () => {
                        if (this.checkRules(this.controlStructure.displayRules))
                        {
                            this.controlLoader.reloadControl(this, () => {
                                if ('afterReloadDependentOption' in this) {
                                    this.afterReloadDependentOption();
                                } else {
                                    this.optionValue = this.getEmptyValue();
                                }
                            });

                            this.needReloadControl = false;
                        }
                        else
                        {
                            this.needReloadControl = true;
                        }
                    });
                }
            }

            if ('initializeReloadRules' in target.prototype)
            {
                super.initializeReloadRules();
            }
        }
    };
}


