import ComponentAbstract from 'Admin/Component/ComponentAbstract';
import EventManager from 'System/Event/EventManager';

import BasePanel from 'Admin/Editor/Panel/BasePanel';

export default class Builder extends ComponentAbstract
{
    public initialProperties(): void
    {
        this.app.componentRegister.set('Editor/Builder/Panel', this);
    }

    public attachEvents(manager: EventManager): void {}

    public createPanel(key: string, context: object, entityId?: any): BasePanel
    {
        if (!this.prototypes.panel.has(key))
        {
            throw new Error(`Panel controller with id '${key}' not found`);
        }

        if (entityId && !this.register.components.has(entityId))
        {
            throw new Error(`Editor element with id '${entityId}' not found`);
        }

        let panelPrototype = this.prototypes.panel.get(key),
            panel = this.componentBuilder.build(panelPrototype);

        panel.panelKey = key;

        panel.panelContext = context;

        if (!entityId && panel.hasOwnProperty('editorComponent'))
        {
            throw new Error(`For controller of '${key}' panel, need entity`);
        }

        if (entityId)
        {
            let editorComponent = this.register.components.get(entityId);

            (panel as any).editorComponent = editorComponent;

            if (editorComponent.hasOwnProperty('panels'))
            {
                editorComponent.panels.add(panel);
            }
        }

        return panel;
    }
}


