import ComponentAbstract from 'Admin/Component/ComponentAbstract';
import EventManager from 'System/Event/EventManager';

import FieldListPanel from 'Admin/Field/Editor/FieldListPanel';
import FieldListComponent from 'Admin/Field/Editor/FieldListComponent';
import FieldProjection from 'Admin/Field/Editor/FieldProjection';

export default class Registration extends ComponentAbstract
{
    public attachEvents(manager: EventManager): void
    {
        manager.addSystemHandler(this.editorPackage.initEditorEvent, this.initEditor);
    }

    public initEditor(prototypes: any): void
    {
        prototypes.component.set('DropDownListField', FieldListComponent);

        prototypes.panel.set('DropDownListField', FieldListPanel);

        prototypes.projection.set('DropDownListField', FieldProjection);
    }
}


