import GroupProjectionAbstract from 'Admin/Editor/Projection/GroupProjectionAbstract';
import EventManager from 'System/Event/EventManager';

abstract class LayoutProjectionAbstract extends GroupProjectionAbstract
{
    public attachEvents(manager: EventManager): void
    {
        super.attachEvents(manager);

        this.addElementHandler(
            'click',
            `.actions-wrapper.entity-id-${this.editorComponent.entityId} .remove-action`,
            this.onRemoveClick
        );

        this.addElementHandler(
            'click',
            `.actions-wrapper.entity-id-${this.editorComponent.entityId} .edit-action`,
            this.onEditClick
        );
    }

    protected onRemoveClick(event: Event): void
    {
        this.projectionManager.removeProjection(this.editorComponent, true);
    }

    protected onEditClick(event: Event): void
    {
        this.panelManager.openPanel(
            this.editorComponent.entityKey,
            {},
            this.editorComponent.entityId
        );
    }

    public getTemplateContext(): object
    {
        return {
            entity: this.entity
        };
    }
}

export default LayoutProjectionAbstract;


