import EditorComponentPanel from 'Admin/Editor/Panel/EditorComponentPanel';

import ExtendObject from 'System/Object/ExtendObject';

export default class PanelController extends EditorComponentPanel
{
    public getValidatorOptions(): object
    {
        return ExtendObject(super.getValidatorOptions(), {
            rules: {
                entityTitle: {
                    required: true,
                    minlength: 2
                },
                optionKey: {
                    requiredIfActiveControl: true,
                    filterKeyText: true,
                    filterKeyUniquer: true,
                    minlength: 2
                },
                minPriceOptionKey: {
                    requiredIfActiveControl: true,
                    filterKeyText: true,
                    filterKeyUniquer: true,
                    minlength: 2
                },
                maxPriceOptionKey: {
                    requiredIfActiveControl: true,
                    filterKeyText: true,
                    filterKeyUniquer: true,
                    filterKeyUniquerDepends: ['minPriceOptionKey'],
                    minlength: 2
                }
            }
        });
    }
}


