import EventManager from 'System/Event/EventManager';
import ApplicationEvent from 'System/ApplicationEvent';

import ExtendObject from 'System/Object/ExtendObject';

import BasePanel from 'Admin/Editor/Panel/BasePanel';
import IValidationPanel from 'Admin/Editor/Panel/IValidationPanel';
import IValidateEventArgs from 'Admin/Editor/Panel/EventArgs/IValidateEventArgs';
import IAfterInitValidationEventArgs from 'Admin/Editor/Panel/EventArgs/IAfterInitValidationEventArgs';

export default class ValidationPanel extends BasePanel implements IValidationPanel
{
    public readonly validateEvent: ApplicationEvent<IValidateEventArgs>;

    public readonly afterInitValidationEvent: ApplicationEvent<IAfterInitValidationEventArgs>;

    public constructor()
    {
        super();

        this.afterInitValidationEvent = new ApplicationEvent<IAfterInitValidationEventArgs>();

        this.validateEvent = new ApplicationEvent<IValidateEventArgs>();
    }

    public attachEvents(manager: EventManager): void
    {
        super.attachEvents(manager);

        for (let control of this.panelLayout.controls.values())
        {
            manager.addSystemHandler(control.valueChangedEvent, this.validate);
        }
    }

    public onLaunched(): void
    {
        let options = this.getValidatorOptions();

        let validator = this.formElement.validate(options);

        if (validator && validator.hasOwnProperty('settings'))
        {
            validator.settings = ExtendObject(validator.settings, options);
        }

        this.afterInitValidationEvent.trigger({
            panel: this,
            formElement: this.formElement,
            options: options
        });
    }

    protected onFormSubmit(event: Event): void
    {
        super.onFormSubmit(event);

        this.saveValuesOfControls();
    }

    public validate(): boolean
    {
        let isValid = this.formElement.valid();

        this.validateEvent.trigger({
            isValid: isValid,
            panel: this
        });

        return isValid;
    }

    public saveValuesOfControls(): void
    {
        if (this.validate())
        {
            super.saveValuesOfControls();
        }
    }

    public resetValuesOfControls(): void
    {
        let isControlsSaved = this.panelLayout.isControlsSaved;

        super.resetValuesOfControls();

        if (!isControlsSaved)
        {
            this.validate();
        }
    }

    public getValidatorOptions(): object
    {
        return {};
    }
}


