import EventManager from 'System/Event/EventManager';
import BaseControl from 'Admin/Editor/Control/BaseControl';
import CloneVariable from 'System/Object/CloneVariable';

export default class RulesBuilderControl extends BaseControl
{
    protected isRestoredOption: boolean = false;

    protected static optionsElements: any = {};

    protected static optionsQueries: any = {};

    public getEmptyValue(): any
    {
        return [];
    }

    public getGroupPosition(groupId: string): any
    {
        for (let index in this.optionValue)
        {
            let group = this.optionValue[index];

            if (group.id == groupId)
            {
                return index;
            }
        }

        return -1;
    }

    public getRulePosition(groupId: string, ruleId: string): any
    {
        let rules = this.getGroupOption(groupId).rules;

        for (let index in rules)
        {
            let rule = rules[index];

            if (rule.id == ruleId)
            {
                return index;
            }
        }

        return -1;
    }

    public getGroupOption(groupId: string): any
    {
        for (let index in this.optionValue)
        {
            let group = this.optionValue[index];

            if (group.id == groupId)
            {
                return group;
            }
        }

        return null;
    }

    public getRuleOption(groupId: string, ruleId: string): any
    {
        let index = this.getRulePosition(groupId, ruleId),
            group = this.getGroupOption(groupId);

        if (index !== -1)
        {
            return group.rules[index];
        }

        return null;
    }

    public setRuleOption(groupId: string, ruleId: string, options: any, position?): void
    {
        let index = this.getRulePosition(groupId, ruleId),
            group = this.getGroupOption(groupId);

        if (index !== -1)
        {
            group.rules[index] = {
                id: ruleId,
                rule: options
            };
        }
        else
        {
            if (position)
            {
                group.rules.splice(position, 0, {
                    id: ruleId,
                    rule: options
                });
            }
            else
            {
                group.rules.push({
                    id: ruleId,
                    rule: options
                });
            }
        }
    }

    public hasRuleOption(groupId: string, ruleId: string): boolean
    {
        let rules = this.getGroupOption(groupId).rules;

        for (let index in rules)
        {
            let rule = rules[index];

            if (rule.id == ruleId)
            {
                return true;
            }
        }

        return false;
    }

    public hasGroupOption(groupId: string): boolean
    {
        for (let index in this.optionValue)
        {
            let group = this.optionValue[index];

            if (group.id == groupId)
            {
                return true;
            }
        }

        return false;
    }

    public deleteRuleOption(groupId: string, ruleId: string): void
    {
        let group = this.getGroupOption(groupId),
            position = this.getRulePosition(groupId, ruleId);

        if (position !== -1)
        {
            group.rules.splice(position, 1);
        }
    }

    public deleteGroupOption(groupId: string): void
    {
        let position = this.getGroupPosition(groupId);

        if (position !== -1)
        {
            this.optionValue.splice(position, 1);
        }
    }

    public restoreOptionValue(): void
    {
        this.isRestoredOption = true;

        super.restoreOptionValue();

        if (typeof this.optionValue != 'object'
            || !this.optionValue
            || !this.hasGroupOption('group_0')
            || !this.hasRuleOption('group_0', 'rule_0'))
        {
            if (typeof this.optionValue != 'object' || !this.optionValue)
            {
                this.optionValueProp = [];
            }

            if (!this.hasGroupOption('group_0'))
            {
                this.optionValueProp.push({
                    id: 'group_0',
                    rules: []
                });
            }

            let defaultGroup = this.getGroupOption('group_0');

            defaultGroup.rules.push({
                id: 'rule_0',
                rule: {
                    param: (this.controlStructure as any).useEntries[0],
                    operator: '==',
                    value: null
                }
            });
        }

        let preGroupElement = null;

        for (let groupIndex in this.optionValue)
        {
            let group = this.optionValue[groupIndex],
                groupElement = this.getGroupElement(group.id);

            if (!groupElement.length && group.rules)
            {
                this.createEmptyGroup(group.id, preGroupElement);

                groupElement = this.getGroupElement(group.id);
            }
            else if (groupElement.length && !group.rules)
            {
                this.deleteGroupOption(group.id);

                continue;
            }

            let prevRuleElement = null;

            for (let ruleIndex in group.rules)
            {
                let rule = group.rules[ruleIndex],
                    ruleElement = this.getRuleElement(group.id, rule.id);

                if (!ruleElement.length)
                {
                    this.createEmptyRule(group.id, rule.id, prevRuleElement);

                    ruleElement = this.getRuleElement(group.id, rule.id);
                }

                this.setRuleOptions(group.id, rule.id, rule.rule);

                prevRuleElement = ruleElement;
            }

            preGroupElement = groupElement;
        }

        this.componentElement.find('.rule-group[data-id]').each((groupIndex, groupDomElement) => {
            let groupElement = jQuery(groupDomElement),
                groupId = groupElement.data('id');

            if (!this.hasGroupOption(groupId))
            {
                groupElement.remove();

                return;
            }

            groupElement.find('.rule-row[data-id]').each((ruleIndex, ruleDomElement) => {
                let ruleElement = jQuery(ruleDomElement),
                    ruleId = ruleElement.data('id');

                if (!this.hasRuleOption(groupId, ruleId))
                {
                    ruleElement.remove();

                    return;
                }
            });
        });

        this.initSelect2();

        this.isRestoredOption = false;
    }

    protected initSelect2(): void
    {
        (this.componentElement.find('.value-select') as any).select2({
            width: '100%',
            
            closeOnSelect: true
        });
    }

    protected setRuleOptions(groupId, ruleId, options, position?): void
    {
        let ruleElement = this.getRuleElement(groupId, ruleId);

        ruleElement.find('.operator-select').val(options.operator);

        ruleElement.find('.param-select').val(options.param);

        let prevOption = CloneVariable(this.optionValue);

        this.setRuleOption(groupId, ruleId, options, position);

        if (!this.isRestoredOption)
        {
            this.valueChangedEvent.trigger({
                oldValue: prevOption,
                value: this.optionValue,
                control: this
            });
        }

        this.reloadEntries(groupId, ruleId);
    }

    public attachEvents(manager: EventManager): void
    {
        super.attachEvents(manager);

        manager.addElementHandler(
            this.componentElement,
            'change',
            '.rule-row .param-select',
            this.onParamChange
        );

        manager.addElementHandler(
            this.componentElement,
            'change',
            '.rule-row .operator-select',
            this.onOperatorChange
        );

        manager.addElementHandler(
            this.componentElement,
            'change',
            '.rule-row .value-select',
            this.onEntryValueChange
        );

        manager.addElementHandler(
            this.componentElement,
            'click',
            '.rule-row .add-rule.button',
            this.onAddRuleButtonClick
        );

        manager.addElementHandler(
            this.componentElement,
            'click',
            '.rule-row .remove-rule.button',
            this.onRemoveRuleButtonClick
        );

        manager.addElementHandler(
            this.componentElement,
            'click',
            '.rule-groups .add-rule-group.button',
            this.onAddGroupButtonClick
        );
    }

    protected compileTemplate(html: string, context: any): string
    {
        let template = (<any> window)._.template(html, {
            evaluate:    /<#([\s\S]+?)#>/g,
            interpolate: /\{\{\{([\s\S]+?)\}\}\}/g,
            escape:      /\{\{([^\}]+?)\}\}(?!\})/g,
            variable:    'data'
        });

        return template(context);
    }

    protected getRuleElement(groupId: string, ruleId: string): any
    {
        return this.componentElement.find(`.rule-group[data-id="${groupId}"] .rule-row[data-id="${ruleId}"]`);
    }

    protected getGroupElement(groupId: string): any
    {
        return this.componentElement.find(`.rule-group[data-id="${groupId}"]`);
    }

    protected generateUniqueId(): string
    {
        return '_' + Math.random().toString(36).substr(2, 9);
    }

    protected onRemoveRuleButtonClick(event): void
    {
        let buttonElement = jQuery(event.currentTarget),
            ruleElement = buttonElement.parents('.rule-row:first'),
            ruleId = ruleElement.data('id'),
            groupId = ruleElement.parents('.rule-group:first').data('id'),
            rulesContainerElement = ruleElement.parents('.rules-table-body');

        let prevOption = CloneVariable(this.optionValue);

        if (ruleId == 'rule_0' && groupId == 'group_0')
        {
            return;
        }

        if (rulesContainerElement.find('.rule-row').length <= 1 && groupId != 'group_0')
        {
            this.getGroupElement(groupId).remove();

            this.deleteGroupOption(groupId);
        }
        else
        {
            ruleElement.remove();

            this.deleteRuleOption(groupId, ruleId);
        }

        this.valueChangedEvent.trigger({
            oldValue: prevOption,
            value: this.optionValue,
            control: this
        });
    }

    protected onAddGroupButtonClick(event): void
    {
        let newGroupId = this.generateUniqueId(),
            groupHtml = this.compileTemplate((this.controlStructure as any).groupRuleTemplateHtml, {
                groupId: newGroupId
            }),
            groupElement = jQuery(groupHtml),
            newRuleId = this.generateUniqueId();

        if (this.componentElement.find('.rule-group').length)
        {
            groupElement.insertAfter(
                this.componentElement.find('.rule-group:last')
            );
        }
        else
        {
            this.componentElement.find('.rule-groups').prepend(groupElement);
        }

        let ruleOption = {
            param: (this.controlStructure as any).useEntries[0],
            operator: '==',
            value: null
        };

        this.optionValue.push({
            id: newGroupId,
            rules: []
        });

        this.createEmptyRule(newGroupId, newRuleId);

        this.setRuleOptions(newGroupId, newRuleId, ruleOption);
    }

    protected createEmptyGroup(newGroupId, prevGroupElement?: any): void
    {
        let groupHtml = this.compileTemplate((this.controlStructure as any).groupRuleTemplateHtml, {
                groupId: newGroupId
            }),
            groupElement = jQuery(groupHtml);

        if (prevGroupElement)
        {
            groupElement.insertAfter(prevGroupElement);
        }
        else
        {
            if (this.componentElement.find('.rule-group').length)
            {
                groupElement.insertAfter(
                    this.componentElement.find('.rule-group:last')
                );
            }
            else
            {
                this.componentElement.find('.rule-groups').prepend(groupElement);
            }
        }
    }

    protected createEmptyRule(groupId, newRuleId, prevRuleElement?: any): void
    {
        let ruleHtml = this.compileTemplate((this.controlStructure as any).ruleTemplateHtml, {
                groupId: groupId,
                ruleId: newRuleId
            }),
            ruleElement = jQuery(ruleHtml);

        if (prevRuleElement)
        {
            ruleElement.insertAfter(prevRuleElement);
        }
        else
        {
            this.getGroupElement(groupId).find('.rules-table-body').append(ruleElement);
        }

        this.initSelect2();
    }

    protected onAddRuleButtonClick(event): void
    {
        let buttonElement = jQuery(event.currentTarget),
            ruleId = buttonElement.parents('.rule-row:first').data('id'),
            groupId = buttonElement.parents('.rule-group:first').data('id'),
            prevRuleElement = this.getRuleElement(groupId, ruleId),
            newRuleId = this.generateUniqueId();

        this.createEmptyRule(groupId, newRuleId, prevRuleElement);

        let group = this.getGroupOption(groupId),
            pevPosition =  this.getRulePosition(groupId, ruleId),
            options = {
                param: (this.controlStructure as any).useEntries[0],
                operator: '==',
                value: null
            };

        group.rules.splice(pevPosition + 1, 0, {
            id: newRuleId,
            rule: options
        });

        this.setRuleOptions(groupId, newRuleId, options, pevPosition + 1);
    }

    protected onEntryValueChange(event): void
    {
        let selectElement = jQuery(event.currentTarget),
            ruleId = selectElement.parents('.rule-row:first').data('id'),
            groupId = selectElement.parents('.rule-group:first').data('id'),
            operator = selectElement.val();

        this.changeRuleOption(groupId, ruleId, 'value', operator);
    }

    protected onOperatorChange(event): void
    {
        let selectElement = jQuery(event.currentTarget),
            ruleId = selectElement.parents('.rule-row:first').data('id'),
            groupId = selectElement.parents('.rule-group:first').data('id'),
            operator = selectElement.val();

        this.changeRuleOption(groupId, ruleId, 'operator', operator);
    }

    protected onParamChange(event): void
    {
        let selectElement = jQuery(event.currentTarget),
            ruleId = selectElement.parents('.rule-row:first').data('id'),
            groupId = selectElement.parents('.rule-group:first').data('id'),
            param = selectElement.val();

        this.changeRuleOption(groupId, ruleId, 'param', param);

        this.reloadEntries(groupId, ruleId);
    }

    protected reloadEntries(groupId: string, ruleId: string): void
    {
        let param = this.getRuleOption(groupId, ruleId).rule.param,
            optionElement = this.getValueElementByRule(groupId, ruleId);

        optionElement.data('wcpf-loaded-options', false);

        optionElement.parents('td:first').block();

        if (RulesBuilderControl.optionsElements.hasOwnProperty(param)) {
            this.updateElementsWithParams(param);
        } else if (!RulesBuilderControl.optionsQueries.hasOwnProperty(param)
            || (RulesBuilderControl.optionsQueries[param].readyState == 4 && RulesBuilderControl.optionsQueries[param].status != 200)) {
            RulesBuilderControl.optionsQueries[param] = wp.ajax.send('wcpf_editor_rules_builder_get_options', {
                type: 'POST',
                dataType: 'json',
                data: {
                    param: param
                }
            }).done((data) => {
                if (data && data.hasOwnProperty('optionsHtml')) {
                    RulesBuilderControl.optionsElements[param] = jQuery(data.optionsHtml);

                    this.updateElementsWithParams(param);
                } else if (RulesBuilderControl.optionsQueries.hasOwnProperty(param)) {
                    delete RulesBuilderControl.optionsQueries[param];
                }
            });
        }
    }

    protected updateElementsWithParams(param: string): void
    {
        if (typeof this.optionValue != 'object' || !this.optionValue)
        {
            return;
        }

        for (let groupIndex in this.optionValue)
        {
            let group = this.optionValue[groupIndex];

            for (let ruleIndex in group.rules)
            {
                let ruleItem = group.rules[ruleIndex];

                if (ruleItem.rule.param != param)
                {
                    continue;
                }

                let valueElement = this.getValueElementByRule(group.id, ruleItem.id);

                if (!valueElement.data('wcpf-loaded-options'))
                {
                    valueElement.html(
                        RulesBuilderControl.optionsElements[param].html()
                    );

                    valueElement.data('wcpf-loaded-options', true);

                    valueElement.parents('td:first').unblock();
                }

                this.isRestoredOption = true;

                valueElement.val(ruleItem.rule.value);

                valueElement.trigger('change');

                this.isRestoredOption = false;
            }
        }
    }

    protected getValueElementByRule(groupId: string, ruleId: string)
    {
        return this.componentElement.find(`.rule-group[data-id="${groupId}"] .rule-row[data-id="${ruleId}"] .value-select`);
    }

    protected changeRuleOption(groupId: string, ruleId: string, optionName: string, optionValue: any): void
    {
        if (!this.optionValue || typeof this.optionValue != 'object') {
            this.optionValueProp = [];
        }

        let ruleItem = this.getRuleOption(groupId, ruleId);

        if (ruleItem && ruleItem.rule[optionName] == optionValue) {
            return;
        }

        let prevOption = CloneVariable(this.optionValue);

        ruleItem.rule[optionName] = optionValue;

        if (!this.isRestoredOption)
        {
            this.valueChangedEvent.trigger({
                oldValue: prevOption,
                value: this.optionValue,
                control: this
            });
        }
    }
}


