import EventManager from 'System/Event/EventManager';

import InputControlAbstract from 'Admin/Editor/Control/InputControlAbstract';

export default class SwitchControl extends InputControlAbstract
{
    public get firstOption(): any
    {
        return (this.controlStructure as any).firstOption;
    }

    public get secondOption(): any
    {
        return (this.controlStructure as any).secondOption;
    }

    public attachEvents(manager: EventManager): void
    {
        super.attachEvents(manager);

        manager.addElementHandler(
            this.controlElement,
            'change',
            this.updateValueFromElement
        );
    }

    public setElementValue(value: any): void
    {
        if (value == this.firstOption.value)
        {
            this.controlElement.prop('checked', true);
        }
        else
        {
            this.controlElement.prop('checked', false);
        }
    }

    public getElementValue(): any
    {
        return this.controlElement.prop('checked') ? this.firstOption.value : this.secondOption.value;
    }
}


