import BaseControl from 'Admin/Editor/Control/BaseControl';
import EventManager from 'System/Event/EventManager';
import ReloadControl from 'Admin/Editor/Control/Decorators/ReloadControl';
import IReloadControl from 'Admin/Editor/Control/IReloadControl';
import PanelStatus from 'Admin/Editor/Panel/PanelStatus';
import RadioListControl from 'Admin/Editor/Control/RadioListControl';

import CreateElement from 'System/Helper/CreateElement';
import ObjectToArray from 'System/Object/ObjectToArray';
import CloneVariable from 'System/Object/CloneVariable';
import ArrayCompare from 'System/Object/ArrayCompare';

@ReloadControl
export default class RepeaterControl extends BaseControl implements IReloadControl
{
    protected childControls = [];

    protected reloadControlsQueue = [];

    public get controlLoader(): any
    {
        return this.app.componentRegister.get('Editor/Control/ControlLoader');
    }

    protected get itemsElement(): JQuery
    {
        return this.listElement.find('> .repeater-item');
    }

    protected get listElement(): JQuery
    {
        return this.componentElement.find('.repeater-list:first');
    }

    public initialProperties(): void
    {
        super.initialProperties();

        this.listElement.sortable({
            items: '> .repeater-item',
            handle: '.sorting-repeater-item-handle',
            update: (event, ui) => {
                this.onItemMove();
            }
        });
    }

    public restoreOptionValue(): void
    {
        let value = this.controlSource.getValue();

        if (typeof value != 'object')
        {
            value = [];
        }

        this.optionValueProp = value;

        this.changeValue(value);
    }

    protected initializeReloadRules(): void
    {
        let itemOptionsDepends = (this.controlStructure as any).itemOptionsDepends,
            usedDepends = [];

        if (!Object.keys(itemOptionsDepends).length)
        {
            return;
        }

        for (let controlIndex in itemOptionsDepends)
        {
            let optionsDepends = itemOptionsDepends[controlIndex];

            for (let dependIndex of optionsDepends.values())
            {
                if (usedDepends.indexOf(dependIndex) !== -1)
                {
                    continue;
                }

                usedDepends.push(dependIndex);

                let dependControl = this.getControlByOptionKey(dependIndex);

                if (!dependControl)
                {
                    continue;
                }

                this.eventManager.addSystemHandler(dependControl.valueChangedEvent, this.onItemDependChange);
            }
        }
    }

    public attachEvents(manager: EventManager): void
    {
        super.attachEvents(manager);

        if (this.panel.hasOwnProperty('afterInitValidationEvent'))
        {
            manager.addSystemHandler(
                (this.panel as any).afterInitValidationEvent,
                this.addRulesToRepeaterItems
            );
        }

        manager.addElementHandler(
            this.componentElement,
            'click',
            '.repeater-bottom-navigation .add-item',
            this.onAddItemClick
        );

        manager.addElementHandler(
            this.componentElement,
            'click',
            `.navigation-repeater-item[data-repeater-key="${this.optionKey}"] .add-empty-item`,
            this.onAddBeforeItemClick
        );

        manager.addElementHandler(
            this.componentElement,
            'click',
            `.navigation-repeater-item[data-repeater-key="${this.optionKey}"] .remove-item`,
            this.onRemoveItemClick
        );

        manager.addSystemHandler(
            this.valueChangedEvent,
            this.onValueChangedEvent
        );
    }

    protected onItemMove(): void
    {
        let newOptionValue = [],
            newChildControls = [];

        this.itemsElement.each((indexElement, domElement) => {
            let itemElement = CreateElement(domElement),
                itemIndex = itemElement.attr('data-index');

            if (itemElement.hasClass('ui-sortable-placeholder'))
            {
                return;
            }

            newOptionValue.push(this.optionValue[itemIndex]);

            newChildControls.push(this.childControls[itemIndex]);
        });

        this.childControls = newChildControls;

        this.optionValue = CloneVariable(newOptionValue);
    }

    protected onValueChangedEvent(args: any): void
    {
        this.changeValue(args.value);
    }

    protected onRemoveItemClick(event: Event): void
    {
        let buttonElement = CreateElement(event.currentTarget),
            itemElement = buttonElement.parents('.repeater-item:first'),
            itemIndex = parseInt(itemElement.attr('data-index')),
            oldValue = CloneVariable(this.optionValue);

        itemElement.remove();

        this.optionValue.splice(itemIndex, 1);

        this.childControls.splice(itemIndex, 1);

        this.refreshIndexes();

        this.changeEventWithoutProcessing({
            oldValue: oldValue,
            newValue: this.optionValue
        });
    }

    protected onAddBeforeItemClick(event: Event): void
    {
        let buttonElement = CreateElement(event.currentTarget),
            itemElement = buttonElement.parents('.repeater-item:first'),
            itemIndex = parseInt(itemElement.attr('data-index')),
            oldValue = CloneVariable(this.optionValue);

        this.optionValue.splice(itemIndex, 0, {});

        this.childControls.splice(itemIndex, 0, {});

        this.createItem(itemIndex, {
            refreshIndexes: true
        });

        this.changeEventWithoutProcessing({
            oldValue: oldValue,
            newValue: this.optionValue
        });
    }

    protected onAddItemClick(event: Event): void
    {
        this.createItem(-1);
    }

    protected changeValue(value: any): void
    {
        if (value == undefined || value == null)
        {
            value = [];
        }
        else if (!Array.isArray(value) && typeof value == 'object')
        {
            value = ObjectToArray(value);

            this.optionValueProp = value;
        }

        if (!Array.isArray(value))
        {
            return;
        }

        if (value.length < this.childControls.length)
        {
            for (let i = value.length; i < this.childControls.length; i++)
            {
                let firstControlIndex = Object.keys(this.childControls[i])[0],
                    firstControl = this.childControls[i][firstControlIndex],
                    repeaterItemElement = firstControl.componentElement.parents('.repeater-item:first');

                repeaterItemElement.remove();
            }

            this.childControls.splice(value.length);
        }

        this.refreshIndexes();

        for (let [index, values] of value.entries())
        {
            if (this.childControls.length - 1 >= index)
            {
                for (let itemIndex in this.childControls[index])
                {
                    let itemControl = this.childControls[index][itemIndex];

                    if (values.hasOwnProperty(itemIndex))
                    {
                        itemControl.optionValue = values[itemIndex];
                    }
                    else
                    {
                        if (itemControl.controlStructure.defaultValue)
                        {
                            itemControl.optionValue = itemControl.controlStructure.defaultValue;
                        }
                        else
                        {
                            itemControl.optionValue = itemControl.getEmptyValue();
                        }
                    }
                }
            }
            else
            {
                this.createItem(index, {
                    reloadAfterInit: false
                });
            }
        }

        if (this.reloadControlsQueue.length)
        {
            this.controlLoader.reloadControls(this.reloadControlsQueue, (controls) => {
                for (let control of controls)
                {
                    control.restoreOptionValue();
                }
            });

            this.reloadControlsQueue = [];
        }
    }

    protected refreshIndexes(): void
    {
        for (let [index, values] of this.optionValue.entries())
        {
            let repeaterItemElement = this.itemsElement.eq(index);

            repeaterItemElement.attr('data-index', index);

            repeaterItemElement.find(`.sorting-repeater-item-handle[data-repeater-key="${this.optionKey}"] .index`)
                .html((index + 1) + '');

            for (let itemIndex in this.childControls[index])
            {
                let itemControl = this.childControls[index][itemIndex],
                    oldOptionKey = itemControl.optionKey,
                    newOptionKey  = this.optionKey + '.' + index + '.' + itemIndex;

                itemControl.componentElement.attr('data-option-key', newOptionKey);

                itemControl.controlStructure.optionKey = newOptionKey;

                (itemControl.controlSource as any).optionKey = index + '.' + itemIndex;

                itemControl.componentElement.find(`[name="${oldOptionKey}"]`).attr('name', newOptionKey);

                if (itemControl instanceof RadioListControl) {
                    itemControl.setElementValue(itemControl.optionValue);
                }
            }
        }

        // reload errors
        let validator = this.panel.formElement.data('validator');

        if (validator && Object.keys(validator.invalid).length) {
            for (let invalidFieldId in validator.invalid) {
                this.panel.formElement.find(`[id="${invalidFieldId}-error"]`).remove();
            }
        }
    }

    public reloadControl(props: any): void
    {
        let needResetValue = (this.controlStructure as any).resetValueOnReload;

        if (needResetValue)
        {
            this.optionValue = [];
        }

        let newItemTemplate = props.controlElement.find('> .control-content > .repeater-item-template');

        this.componentElement.find('> .control-content > .repeater-item-template').html(newItemTemplate.html());

        if (!needResetValue)
        {
            this.changeValue(this.optionValue);
        }
    }

    protected renderRepeaterItem(index): string
    {
        return `
            <div class="repeater-item" data-index="${index}">
                <div class="repeater-item-handle sorting-repeater-item-handle" data-repeater-key="${this.optionKey}">
                    <span class="index">${index + 1}</span>
                </div>
                <div class="repeater-item-controls"></div>
                <div class="repeater-item-handle navigation-repeater-item" data-repeater-key="${this.optionKey}">
                    <div class="add-empty-item"></div>
                    <div class="remove-item"></div>
                </div>
            </div>
        `;
    }

    protected addRulesToRepeaterItems(): void
    {
        let formValidator = this.panel.formElement.data('validator');

        if (!formValidator)
        {
            return;
        }

        let rules = formValidator.settings.rules,
            repeaterRules: any = false;

        if (rules.hasOwnProperty(this.optionKey))
        {
            repeaterRules = rules[this.optionKey];
        }
        else
        {
            for (let ruleOptionKey in rules)
            {
                if  (ruleOptionKey.indexOf('*') !== -1)
                {
                    continue;
                }

                let ruleTemplate = ruleOptionKey.replace(/\./g, '\\.').split('*').join('([a-zA-Z\\d]+)'),
                    ruleRegEx = new RegExp(ruleTemplate);

                if (ruleRegEx.test(this.optionKey))
                {
                    repeaterRules = rules[ruleOptionKey];

                    break;
                }
            }
        }

        if (!repeaterRules
            || typeof repeaterRules != 'object'
            || !repeaterRules.hasOwnProperty('repeaterItem')
            || typeof repeaterRules.repeaterItem != 'object' )
        {
            return;
        }

        for (let index in this.childControls)
        {
            for (let controlIndex in this.childControls[index])
            {
                let control = this.childControls[index][controlIndex];

                if (repeaterRules.repeaterItem.hasOwnProperty(controlIndex))
                {
                    rules[control.optionKey] = repeaterRules.repeaterItem[controlIndex];
                }
            }
        }
    }

    public createItem(index: number, props?: any): any
    {
        props = Object.assign({
            reloadAfterInit: true,
            refreshIndexes: false
        }, props);

        if (index == -1)
        {
            index = this.childControls.length;
        }

        this.childControls[index] = {};

        let contextControls = new Map<string, BaseControl>(),
            structures = (this.controlStructure as any).controlStructures,
            repeaterItemElement = CreateElement(this.renderRepeaterItem(index)),
            controlsElement = repeaterItemElement.find('.repeater-item-controls'),
            nextItemElement = this.componentElement.find('.repeater-list > .repeater-item').eq(index);

        if (index != this.childControls.length && nextItemElement.length)
        {
            nextItemElement.before(repeaterItemElement);
        }
        else
        {
            this.componentElement.find('.repeater-list').append(repeaterItemElement);
        }

        if (props.refreshIndexes)
        {
            this.refreshIndexes();
        }

        for (let itemStructureIndex in structures)
        {
            let itemStructure = Object.assign({}, structures[itemStructureIndex]),
                itemOptionKey = this.optionKey + '.' + index + '.' + itemStructureIndex,
                originalKey = itemStructure.optionKey;

            itemStructure.optionKey = itemOptionKey;

            itemStructure.controlSource = 'container';

            let itemControl = this.builders.control.createControl(itemStructure, this.panelLayout),
                itemControlElement = this.componentElement.find(
                    `.repeater-item-template .control[data-control-type="${itemStructure.controlKey}"][data-option-key="${originalKey}"]`
                );

            if (!itemControlElement.length)
            {
                continue;
            }

            if (!props.reloadAfterInit && itemStructure.hasOwnProperty('reloadAfterInit') && itemStructure.reloadAfterInit)
            {
                itemStructure.reloadAfterInit = false;

                this.reloadControlsQueue.push(itemControl);
            }

            contextControls.set(this.optionKey + '.repeaterItem.' + itemStructureIndex, itemControl);

            contextControls.set('repeaterItem.' + itemStructureIndex, itemControl);

            itemControl.contextControls = contextControls;

            itemControlElement = itemControlElement.clone();

            itemControlElement.attr('data-option-key', itemOptionKey);

            itemControlElement.find(`[name="${originalKey}"]`)
                .attr('name', itemOptionKey);

            itemControl.parentControl = this;

            itemControl.componentElement = itemControlElement;

            (itemControl.controlSource as any).optionsContainer = this.optionValueProp;

            (itemControl.controlSource as any).optionKey = index + '.' + itemStructureIndex;

            controlsElement.append(itemControlElement);

            this.componentBuilder.implementation(itemControl.controlSource, this.package);

            this.eventManager.addSystemHandler(itemControl.valueChangedEvent, this.onChildControlValueChanged);

            this.childControls[index][itemStructureIndex] = itemControl;
        }

        for (let controlIndex in this.childControls[index])
        {
            let control = this.childControls[index][controlIndex];

            this.componentBuilder.implementation(control, this.package);
        }

        if (this.panel.status == PanelStatus.Active)
        {
            this.addRulesToRepeaterItems();
        }
    }

    protected onChildControlValueChanged(args: any): void
    {
        let oldValue = CloneVariable(this.optionValue),
            controlSource: any = args.control.controlSource,
            oldOptionContainer = controlSource.optionsContainer;

        if (!Array.isArray(oldValue))
        {
            oldValue = ObjectToArray(this.optionValue);
        }

        let newValue = CloneVariable(oldValue);

        controlSource.optionsContainer = newValue;

        controlSource.saveValue();

        this.optionValueProp = newValue;

        controlSource.optionsContainer = oldOptionContainer;

        if (!ArrayCompare(oldValue, this.optionValue))
        {
            this.changeEventWithoutProcessing({
                oldValue: oldValue,
                newValue: this.optionValue
            });
        }
    }

    protected changeEventWithoutProcessing(params: any)
    {
        this.eventManager.removeSystemHandler(
            this.valueChangedEvent,
            this.onValueChangedEvent
        );

        this.valueChangedEvent.trigger({
            oldValue: params.oldValue,
            value: params.newValue,
            control: this
        });

        this.eventManager.addSystemHandler(
            this.valueChangedEvent,
            this.onValueChangedEvent
        );
    }

    protected onItemDependChange(args: any): void
    {
        let itemOptionsDepends = (this.controlStructure as any).itemOptionsDepends,
            controlOptionKey = args.control.optionKey,
            reloadControlIndexes = [],
            reloadControls = [];

        if (Object.keys(itemOptionsDepends).length)
        {
            for (let controlIndex in itemOptionsDepends)
            {
                let optionsDepends = itemOptionsDepends[controlIndex];

                if (optionsDepends.indexOf(controlOptionKey) !== -1)
                {
                    reloadControlIndexes.push(controlIndex);

                    continue;
                }
            }
        }

        for (let reloadControlIndex of reloadControlIndexes.values())
        {
            for (let [itemIndex, itemControls] of this.childControls.entries())
            {
                if (itemControls.hasOwnProperty(reloadControlIndex))
                {
                    reloadControls.push(itemControls[reloadControlIndex]);
                }
            }
        }

        if (!reloadControls.length)
        {
            return;
        }

        this.controlLoader.reloadControls(reloadControls, (controls) => {
            for (let control of controls)
            {
                control.optionValue = control.getEmptyValue();
            }
        });
    }
}


