import PackageAbstract from 'System/Package/PackageAbstract';
import ComponentAbstract from 'System/Component/ComponentAbstract';
import ApplicationEvent from 'System/ApplicationEvent';

import BaseComponent from 'Admin/Editor/Component/BaseComponent';

import ComponentPackage from 'Admin/Editor/Component/Package';
import PanelPackage from 'Admin/Editor/Panel/Package';
import PanelLayoutPackage from 'Admin/Editor/PanelLayout/Package';
import ProjectionPackage from 'Admin/Editor/Projection/Package';
import ControlPackage from 'Admin/Editor/Control/Package';
import BuilderPackage from 'Admin/Editor/Builder/Package';
import AddElementPanelPackage from 'Admin/Editor/AddElementPanel/Package';

import Dispatcher from 'Admin/Editor/Dispatcher';
import PanelManager from 'Admin/Editor/PanelManager';
import ProjectionManager from 'Admin/Editor/ProjectionManager';

export default class Package extends PackageAbstract
{
    public readonly initEditorEvent: ApplicationEvent<any> = new ApplicationEvent<any>();

    public readonly projectLoadedEvent: ApplicationEvent<any> = new ApplicationEvent<any>();

    public initialProperties(): void
    {
        this.app.packageRegister.set('Editor/Package', this);

        this.app.objectContainer.set('Editor/ProjectEntity', null);

        this.app.objectContainer.set('Editor/ProjectComponent', null);

        this.app.objectContainer.set('Editor/Components', new Map<any, BaseComponent>());
    }

    public getInitialSubpackages(): Object[]
    {
        return [
            new ComponentPackage(),
            new PanelPackage(),
            new PanelLayoutPackage(),
            new ProjectionPackage(),
            new ControlPackage(),
            new BuilderPackage(),
            new AddElementPanelPackage()
        ];
    }

    public getInitialComponents(): ComponentAbstract[]
    {
        return [
            new Dispatcher(),
            new ProjectionManager(),
            new PanelManager()
        ];
    }
}


