import EditorComponentPanel from 'Admin/Editor/Panel/EditorComponentPanel';

export default class EditorPanel extends EditorComponentPanel
{
    public getValidatorOptions(): object
    {
        return {
            rules: {
                entityTitle: {
                    required: true,
                    minlength: 2
                },
                filteringStarts: {
                    required: true
                },
                productsContainerSelector: {
                    requiredIfActiveControl: true
                },
                paginationSelector: {
                    requiredIfActiveControl: true
                },
                resultCountSelector: {
                    requiredIfActiveControl: true
                },
                sortingSelector: {
                    requiredIfActiveControl: true
                }
            }
        };
    }
}


