import ComponentAbstract from 'Admin/Component/ComponentAbstract';
import EventManager from 'System/Event/EventManager';

import Entity from 'App/Entity/Entity';

import FindEntityById from 'App/Entity/Helper/FindEntityById';

import BaseComponent from 'Admin/Editor/Component/BaseComponent';

export default class Builder extends ComponentAbstract
{
    public initialProperties(): void
    {
        this.app.componentRegister.set('Editor/Builder/Component', this);
    }

    public attachEvents(manager: EventManager): void {}

    public createComponentByEntityId(entityId: any): BaseComponent
    {
        let entity = FindEntityById(entityId, this.projectEntity);

        if (entity == null)
        {
            throw new Error(`Entity by id '${entityId}' not found`);
        }

        return this.createComponentByEntity(entity);
    }

    public createComponentByEntity(entity: Entity): BaseComponent
    {
        if (!this.prototypes.component.has(entity.entityKey))
        {
            throw new Error(`Element prototype by entity key '${entity.entityKey}' not found`);
        }

        if (this.isNumeric(entity.entityId))
        {
            entity.entityId = parseFloat(entity.entityId);
        }

        if (this.isNumeric(entity.parentId))
        {
            entity.parentId = parseFloat(entity.parentId);
        }

        let componentPrototype = this.prototypes.component.get(entity.entityKey),
            editorComponent = this.componentBuilder.build(componentPrototype);

        editorComponent.entity = entity;

        this.register.components.set(entity.entityId, editorComponent);

        this.componentBuilder.implementation(editorComponent, this.package);

        if (entity.childEntities.length)
        {
            for (let i in entity.childEntities)
            {
                let itemEntity = entity.childEntities[i];

                this.createComponentByEntity(itemEntity);
            }
        }

        return editorComponent;
    }

    protected isNumeric(value): boolean
    {
        return !isNaN(parseFloat(value)) && isFinite(value);
    }
}


