import PackageAbstract from 'System/Package/PackageAbstract';
import ComponentAbstract from 'System/Component/ComponentAbstract';

import PanelLayoutAbstract from 'Admin/Editor/PanelLayout/PanelLayoutAbstract';

import IPanelStructure from 'Admin/Editor/PanelLayout/IPanelStructure';
import TabsLayout from 'Admin/Editor/PanelLayout/TabsLayout';
import ListLayout from 'Admin/Editor/PanelLayout/ListLayout';

export default class Package extends PackageAbstract
{
    public initialProperties(): void
    {
        this.app.objectContainer.set(
            'Editor/PanelLayout/Prototypes',
            new Map<string, typeof PanelLayoutAbstract>([
                ['Tabs', TabsLayout],
                ['List', ListLayout]
            ])
        );

        this.app.objectContainer.set(
            'Editor/Panel/Structures',
            new Map<string, IPanelStructure>()
        );
    }

    public getInitialComponents(): ComponentAbstract[]
    {
        return [];
    }
}


