import ComponentAbstract from 'System/Component/ComponentAbstract';
import EventManager from 'System/Event/EventManager';

export default class Loader extends ComponentAbstract
{
    protected options: any;

    public initialProperties()
    {
        this.options = {
            evaluate: /<#([\s\S]+?)#>/g,
            interpolate: /\{\{\{([\s\S]+?)\}\}\}/g,
            escape: /\{\{([^\}]+?)\}\}(?!\})/g,
            variable: 'data'
        };

        this.app.componentRegister.set('TemplateLoader', this);
    }

    public attachEvents(eventManager: EventManager): void {}

    public loadTemplate(template: string, data?: any)
    {
        if (data == undefined)
        {
            data = {};
        }

        let templateObject = (window as any)._.template(template, this.options);

        return templateObject(data);
    }
}


