import ITreeEntity from 'System/Entity/ITreeEntity';
import GetEntitySelector from 'System/Helper/GetEntitySelector';

export default function GetEntityElement(entity: ITreeEntity, endSelector?: string): JQuery | null
{
    var selector = GetEntitySelector(entity, endSelector);

    if (! selector)
    {
        return null;
    }

    return jQuery(selector);
}



