import ComponentAbstract from 'Admin/Component/ComponentAbstract';
import EventManager from 'System/Event/EventManager';

import BasePanel from 'Admin/Editor/Panel/BasePanel';
import BaseControl from 'Admin/Editor/Control/BaseControl';

abstract class SourceAbstract extends ComponentAbstract
{
    public control: BaseControl;

    public get panel(): BasePanel
    {
        return this.control.panel;
    }

    public get optionKey(): string
    {
        return this.control.optionKey;
    }

    public get optionValue(): any
    {
        return this.control.optionValue;
    }

    public attachEvents(manager: EventManager): void {}

    public abstract saveValue(): void;

    public abstract getValue(): any;
}

export default SourceAbstract;


