import ValidationPanel from 'Admin/Editor/Panel/ValidationPanel';
import IHasEditorComponent from'Admin/Editor/Panel/IHasEditorComponent';

import BaseComponent from 'Admin/Editor/Component/BaseComponent';

import ObjectRepository from 'App/Option/ObjectRepository';

export default class EditorComponentPanel extends ValidationPanel implements IHasEditorComponent
{
    editorComponent: BaseComponent;

    protected createOptionsRepository(): ObjectRepository
    {
        return this.editorComponent.options;
    }
}


