import InputControlAbstract from 'Admin/Editor/Control/InputControlAbstract';

export default class ColorPickerControl extends InputControlAbstract
{
    protected showControlElement(): void
    {
        this.componentElement.show();
    }

    protected hideControlElement(): void
    {
        this.componentElement.hide();
    }

    public get colorPickerElement(): JQuery
    {
        return this.componentElement.find('.color-picker-element');
    }

    public initialProperties(): void
    {
        let changeHandler = () => {
            this.updateValueFromElement();
        };

        this.colorPickerElement.wpColorPicker({
            hide: true,
            change: changeHandler,
            clear: changeHandler
        });

        super.initialProperties();
    }

    public setElementValue(value: any): void
    {
        if (typeof value != 'string')
        {
            value = '';
        }

        if (value.length)
        {
            this.colorPickerElement.wpColorPicker('color', value);
        }
        else
        {
            (this.colorPickerElement as any).iris('color', '');
        }
    }

    public getElementValue(): any
    {
        return this.colorPickerElement.wpColorPicker('color');
    }
}


