import BasePanel from 'Admin/Editor/Panel/BasePanel';
import EventManager from 'System/Event/EventManager';

import ExtendObject from 'System/Object/ExtendObject';

export default class EditorPanel extends BasePanel
{
    public attachEvents(manager: EventManager): void
    {
        super.attachEvents(manager);

        this.options.observable.add('field', this.onElementSelect, this);

        this.options.observable.add('preset', this.onElementSelect, this);

        this.options.observable.add('layout', this.onElementSelect, this);
    }

    protected onElementSelect(value: any): void
    {
        let elementCode = value.elementCode,
            defaultState = value.defaultState;

        if (!this.register.entityTypes.has(elementCode))
        {
            return;
        }

        let entity = this.entityCollector.collectVirtualEntityByKey(elementCode);

        if (typeof defaultState == 'object')
        {
            entity = ExtendObject(entity, defaultState);
        }

        entity.parentId = this.projectEntity.entityId;

        this.projectEntity.childEntities.push(entity);

        let editorComponent = this.builders.component.createComponentByEntity(entity);

        if (editorComponent.hasOwnProperty('projection'))
        {
            this.projectionManager.addProjection(editorComponent);
        }

        if (this.prototypes.panel.has(entity.entityKey))
        {
            this.panelManager.openPanel(
                entity.entityKey,
                entity.entityId
            );
        }
        else
        {
            this.panelManager.backPanel();

            if (editorComponent.hasOwnProperty('projection'))
            {
                this.projectionManager.scrollToProjection(editorComponent);
            }
        }
    }
}


