import ApplicationEvent from 'System/ApplicationEvent';

import BaseComponent from 'Admin/Editor/Component/BaseComponent';

import BaseProjection from 'Admin/Editor/Projection/BaseProjection';
import IMoveChildProjectionEventArgs from 'Admin/Editor/Projection/EventArgs/IMoveChildProjectionEventArgs';

abstract class GroupProjectionAbstract extends BaseProjection
{
    public moveChildProjectionEvent: ApplicationEvent<IMoveChildProjectionEventArgs>;

    public constructor()
    {
        super();

        this.moveChildProjectionEvent = new ApplicationEvent<IMoveChildProjectionEventArgs>();
    }

    public abstract insertChildComponentElement(element: JQuery, editorComponent: BaseComponent): void;
}

export default GroupProjectionAbstract;


