import BaseProjection from 'Admin/Editor/Projection/BaseProjection';
import EventManager from 'System/Event/EventManager';

export default class FieldProjection extends BaseProjection
{
    public get titleElement(): JQuery
    {
        return this.componentElement.find('.entity-title');
    }

    public attachEvents(manager: EventManager): void
    {
        super.attachEvents(manager);

        this.addElementHandler(
            'click',
            '.actions-wrapper .edit-action',
            this.onEditClick
        );

        this.addElementHandler(
            'click',
            '.actions-wrapper .remove-action',
            this.onRemoveClick
        );

        manager.addSystemHandler(
            this.editorComponent.titleChangedEvent,
            this.onTitleChanged
        );
    }

    protected onRemoveClick(event: Event): void
    {
        this.projectionManager.removeProjection(this.editorComponent, true);
    }

    protected onEditClick(event: Event): void
    {
        this.panelManager.openPanel(
            this.editorComponent.entityKey,
            {},
            this.editorComponent.entityId
        );
    }

    protected onTitleChanged(args: any): void
    {
        this.titleElement.html(args.title);
    }

    public getTemplateContext(): object
    {
        return {
            entity: this.entity
        };
    }
}


