import SourceAbstract from 'Admin/Editor/Control/Source/SourceAbstract';

export default class EntitySource extends SourceAbstract
{
    public saveValue(): void
    {
        if (this.panel.hasOwnProperty('editorComponent'))
        {
            let editorComponent = (this.panel as any).editorComponent;

            if (editorComponent.hasOwnProperty(this.optionKey) || this.optionKey in editorComponent)
            {
                editorComponent[this.optionKey] = this.optionValue;
            }
        }
    }

    public getValue(): any
    {
        if (this.panel.hasOwnProperty('editorComponent'))
        {
            let editorComponent = (this.panel as any).editorComponent;

            if (editorComponent.hasOwnProperty(this.optionKey) || this.optionKey in editorComponent)
            {
                return editorComponent[this.optionKey];
            }
        }

        return undefined;
    }
}


