import PackageAbstract from 'System/Package/PackageAbstract';
import ComponentAbstract from 'System/Component/ComponentAbstract';

import ControlBuilder from 'Admin/Editor/Builder/ControlBuilder';
import PanelBuilder from 'Admin/Editor/Builder/PanelBuilder';
import PanelLayoutBuilder from 'Admin/Editor/Builder/PanelLayoutBuilder';
import ProjectionBuilder from 'Admin/Editor/Builder/ProjectionBuilder';
import ComponentBuilder from 'Admin/Editor/Builder/ComponentBuilder';

export default class Package extends PackageAbstract
{
    public getInitialComponents(): ComponentAbstract[]
    {
        return [
            new ControlBuilder(),
            new PanelBuilder(),
            new PanelLayoutBuilder(),
            new ProjectionBuilder(),
            new ComponentBuilder()
        ];
    }
}


